import React, { useState, useEffect } from "react";
import Header from "./Header";
import axios from "axios";
import { Button } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import "../css/Review.css";

function Review() {
  const urlParams = new URLSearchParams(window.location.search);

  const seletedId = urlParams.get("id");
  console.log(seletedId);

  //    
  const [userReservation, setUserReservation] = useState([]);

  //       
  const [penpickUser, setPenpickUser] = useState([]);

  const [loading, setLoading] = useState(true);

  const [review, setReview] = useState({
    reviewId: "",
    reservation: "",
    penpickUser: "",
    nickName: "",
    reviewCreateDate: "",
    reviewText: "",
    checkInDay: "",
    checkOutDay: "",
  });

  useEffect(() => {
    const handleGetId = async () => {
      try {
        const res = await axios.get(
          "http://localhost:8282/reservation/checkId",
          {
            withCredentials: true,
            params: {
              id: seletedId,
            },
          }
        );
        console.log(" ");
        if (res.data !== null) {
          console.log(res.data);
          setUserReservation(res.data);
        } else {
          console.log("");
        }
      } catch (err) {
        console.error(err + " error");
        console.log(err + " log");
      }
    };
    handleGetId();

    const fetchUserData = async () => {
      try {
        const res = await axios.get("http://localhost:8282/userdata", {
          withCredentials: true,
        });
        setPenpickUser(res.data);
        console.log(res.data);
      } catch (err) {
        console.error("   ", err);
      } finally {
        setLoading(false);
      }
    };
    fetchUserData();

    console.log(" ?");
  }, []);

  useEffect(() => {
    if (userReservation !== null && penpickUser !== null) {
      console.log(userReservation);
      console.log(penpickUser);
      console.log(userReservation.pensions);

      console.log(penpickUser.id);
      console.log(penpickUser.nickname);
    }
  });

  const handleReviewAdd = async () => {
    try {
      // userReservation penpickUser null  
      if (userReservation !== null && penpickUser !== null) {
        // userReservation penpickUser null  ID  newReview  
        const newReview = {
          // reviewId      .
          reservation: userReservation.id, //  ID 
          penpickUser: penpickUser.id, //  ID 
          nickName: penpickUser.nickname, //   
          reviewText: review.reviewText,
          pensions: userReservation.pensions,
          reviewCreateDate: "", //      .
          checkInDay: userReservation.checkInDay,
          checkOutDay: userReservation.checkOutDay,
        };

        //   API 
        const res = await axios.post(
          "http://localhost:8282/review/add",
          newReview
        );
        console.log("  ");
        console.log("", newReview);
      } else {
        console.log("userReservation  penpickUser null.");
      }
    } catch (err) {
      console.log(err);
    }
    alert(" .");
    window.location.href = "/reservationCheck";
  };

  const handleTextInput = (e) => {
    setReview({
      ...review,
      reviewText: e.target.value,
    });
    console.log(review);
  };

  console.log("  ");

  return (
    <div>
      <Header />
      <div id="review-page">
        <div id="review-add-box">
          <div> </div>
          <div>
            <textarea id="review-text" onChange={handleTextInput} />
            <br />
            <Button className="btn" onClick={handleReviewAdd}>
               
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Review;
