import React, { useState, useEffect } from "react";
import axios from "axios";
import "../css/SignUp.css";
import "bootstrap/dist/css/bootstrap.min.css";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import $ from "jquery";

import Header from "./Header";

export default function SignUp() {
  const [data, setData] = useState([]);
  const [newUser, setNewUser] = useState({
    userEmail: "",
    password: "",
    phoneNumber: "",
    gender: "",
    birthday: "",
    nickname: "",
  });
  const [verificationCode, setVerificationCode] = useState("");
  const [isVerificationSent, setIsVerificationSent] = useState(false);
  const [isVerificationConfirmed, setIsVerificationConfirmed] = useState(false);

  //   
  const [isPasswordValid, setIsPasswordValid] = useState(true);

  //   
  const [isNicknameValid, setIsNicknameValid] = useState(true);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const res = await axios.get("http://localhost:8282/api/user", {
          withCredentials: true,
        });
        setData(res.data);
      } catch (error) {
        console.log(error);
      }
    };
    fetchData();
  }, []);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setNewUser((prevUser) => ({ ...prevUser, [name]: value }));
  };

  //    
  const handleGenderChange = (e) => {
    const { value } = e.target;
    setNewUser((prevUser) => ({ ...prevUser, gender: value }));
  };

  const handleSendVerification = async () => {
    try {
      const response = await axios.post(
        "http://localhost:8282/mail",
        { mail: newUser.userEmail },
        {
          withCredentials: true,
        }
      );

      if (response.data === "existUser") {
        alert("  ");
        return;
      }
      if (response.data === "kakaoUser") {
        alert("  ");
        return;
      }

      alert("  .");
      setVerificationCode(response.data);
      setIsVerificationSent(true);
    } catch (error) {
      console.error("   ", error);
    }
  };

  const handleConfirmVerification = () => {
    if (verificationCode === "") {
      alert("   ");
      return;
    }

    setIsVerificationConfirmed(true);
    alert(" ");
  };

  //  
  const handlePasswordChange = (e) => {
    const newPassword = e.target.value;

    //   
    const passwordRegex =
      /^(?=.*[a-zA-Z])(?=.*[!@#$%^&*()_+])(?=.*[0-9]).{8,}$/;
    const isValid = passwordRegex.test(newPassword);

    //     
    setNewUser((prevUser) => ({ ...prevUser, password: newPassword }));
    setIsPasswordValid(isValid);
  };

  //  
  const passwordCheck = () => {
    if ($("#setPassword").val() === $("#checkPassword").val()) {
      $("#pwConfirm").text(" ").css("color", "green");
    } else {
      $("#pwConfirm").text("  ").css("color", "red");
    }
  };

  //    
  const handleAddUser = async () => {
    try {
      if (!isVerificationConfirmed) {
        alert("   ");
        return;
      }

      if (!isPasswordValid) {
        alert("   ");
      }

      //   
      if (
        !newUser.userEmail ||
        !newUser.password ||
        !newUser.phoneNumber ||
        !newUser.gender ||
        !newUser.birthday ||
        !newUser.nickname
      ) {
        alert("  ");
        return;
      }

      //   
      if ($("#setPassword").val() !== $("#checkPassword").val()) {
        alert("    ");
        return;
      }

      const response = await axios.post(
        "http://localhost:8282/api/user/add",
        { ...newUser, verificationCode },
        {
          withCredentials: true,
        }
      );
      setData((prevUser) => [...prevUser, response.data]);
      setNewUser({
        userEmail: "",
        password: "",
        phoneNumber: "",
        gender: "",
        nickname: "",
      });
      setVerificationCode("");
      setIsVerificationSent(false);
      setIsVerificationConfirmed(false);
      alert("PenPick  ");
      window.location.href = "http://localhost:3000/";
    } catch (error) {
      console.error("  ", error);
    }
  };

  return (
    <div>
      <Header />
      <div id="signUpArea">
        <div id="signUpForm">
          <div style={{ width: "100%", marginBottom: "30px" }}>
            <p id="sigh-up-title"></p>
            <p id="signUpMsg">    </p>
          </div>
          <div>
            <label
              style={{ float: "left", fontSize: "small", marginLeft: "2px" }}
            >
              
            </label>
            <div class="dot-badge"></div>
          </div>
          <input
            id="setEmail"
            type="email"
            class="form-control"
            placeholder="yourEmail@penpick.co.kr"
            name="userEmail"
            value={newUser.userEmail}
            onChange={handleInputChange}
            required
          />
          <br />
          <button id="checkEmailButton" onClick={handleSendVerification}>
             
          </button>
          {isVerificationSent && (
            <div>
              <label
                style={{ float: "left", fontSize: "small", marginLeft: "2px" }}
              >
                 
              </label>
              <div class="dot-badge"></div>
              <input
                id="inputEmailcode"
                class="form-control"
                type="text"
                name="verificationCode"
                value={verificationCode}
                onChange={(e) => setVerificationCode(e.target.value)}
                placeholder="  "
              />
              <br />
              <button id="checkEmailButton" onClick={handleConfirmVerification}>
                 
              </button>
            </div>
          )}
          <div>
            <label
              style={{ float: "left", fontSize: "small", marginLeft: "2px" }}
            >
              
            </label>
            <div class="dot-badge"></div>
          </div>
          <input
            id="setPassword"
            type="password"
            class="form-control"
            placeholder=" 8 "
            name="password"
            value={newUser.password}
            onInput={passwordCheck}
            onChange={handlePasswordChange}
            required
          />
          {!isPasswordValid && (
            <>
              <span
                style={{
                  float: "right",
                  marginRight: "3px",
                  marginTop: "2px",
                  color: "red",
                  fontSize: "small",
                }}
              >
                    8  
              </span>
            </>
          )}
          <div style={{ marginTop: "40px" }}>
            <label
              style={{ float: "left", fontSize: "small", marginLeft: "2px" }}
            >
               
            </label>
            <div class="dot-badge"></div>
          </div>
          <input
            id="checkPassword"
            type="password"
            class="form-control"
            placeholder=" "
            onInput={passwordCheck}
            onChange={handleInputChange}
            required
          />
          <span
            id="pwConfirm"
            style={{
              float: "right",
              marginRight: "3px",
              marginTop: "2px",
              color: "gray",
              fontSize: "small",
            }}
          />
          <br />
          <br />
          <div>
            <label
              style={{ float: "left", fontSize: "small", marginLeft: "2px" }}
            >
              
            </label>
            <div class="dot-badge"></div>
          </div>
          <input
            id="setPhondeNumber"
            type="text"
            placeholder=" "
            class="form-control"
            name="phoneNumber"
            value={newUser.phoneNumber}
            onChange={handleInputChange}
          />
          <br />
          <div>
            <label
              style={{ float: "left", fontSize: "small", marginLeft: "2px" }}
            >
              
            </label>
            <div class="dot-badge"></div>
            <br />
          </div>
          <div class="form-check form-check-inline" id="female">
            <input
              class="form-check-input"
              type="radio"
              name="gender"
              id="inlineRadio1"
              value="F"
              onChange={handleGenderChange}
              checked={newUser.gender === "F"}
            />
            <label class="form-check-label" for="inlineRadio1">
              
            </label>
          </div>
          <div class="form-check form-check-inline" id="male">
            <input
              class="form-check-input"
              type="radio"
              name="gender"
              id="inlineRadio2"
              value="M"
              onChange={handleGenderChange}
              checked={newUser.gender === "M"}
            />
            <label class="form-check-label" for="inlineRadio2">
              
            </label>
          </div>
          <br />
          <br />
          <div>
            <label
              style={{ float: "left", fontSize: "small", marginLeft: "2px" }}
            >
              
            </label>
            <div class="dot-badge"></div>
          </div>
          <br />
          <DatePicker
            id="setBirthday"
            selected={newUser.birthday}
            onChange={(date) =>
              setNewUser((prevUser) => ({ ...prevUser, birthday: date }))
            }
            dateFormat="yyyy-MM-dd"
            showYearDropdown
            placeholderText="   "
          />
          <br />
          <br />
          <div>
            <label
              style={{ float: "left", fontSize: "small", marginLeft: "2px" }}
            >
              
            </label>
            <div class="dot-badge"></div>
          </div>
          <input
            id="setNickname"
            type="text"
            class="form-control"
            placeholder=" "
            name="nickname"
            value={newUser.nickname}
            onChange={handleInputChange}
            required
          />
          <button id="signUpButton" type="button" onClick={handleAddUser}>
            Sign Up
          </button>
        </div>
      </div>
    </div>
  );
}
