import React, { useEffect, useState } from 'react';
import axios from 'axios';
import MapImg from '../img/.png';
import NoImg from '../img/.png';
import '../css/PensionList.css';
import {
  Pagination,
  Button,
  Container,
  Row,
  Col,
  InputGroup,
  FormControl,
  Nav,
} from 'react-bootstrap';
import PenPickLogo from '../img/.png';
import Calendar from '../img/.png';
import UserImg from '../img/.png';
import User from '../img/userImg.png';
import SearchButton from '../img/.png';
import { useLocation, useNavigate } from 'react-router-dom';
import Header from './Header';

function PensionList() {
  // 
  const now = new Date();
  const year = now.getFullYear();
  const month = ('0' + (now.getMonth() + 1)).slice(-2);
  const day = ('0' + now.getDate()).slice(-2);

  //   
  const nextDay = new Date(now);
  nextDay.setDate(nextDay.getDate() + 1);
  const nextDayOfMonth = ('0' + nextDay.getDate()).slice(-2);
  const Month = ('0' + (nextDay.getMonth() + 1)).slice(-2);
  const nextYear = nextDay.getFullYear();

  //     
  const oneMonthLater = new Date(now);
  oneMonthLater.setMonth(oneMonthLater.getMonth() + 1);
  const oneMonthLaterYear = oneMonthLater.getFullYear();
  const oneMonthLaterMonth = ('0' + (oneMonthLater.getMonth() + 1)).slice(-2);
  const oneMonthLaterDay = ('0' + oneMonthLater.getDate()).slice(-2);

  //  
  const [currentPage, setCurrentPage] = useState(1);

  //     
  const [pensionsPerPage] = useState(6);

  //     
  const indexOfLastPension = currentPage * pensionsPerPage;

  const indexFirstPension = indexOfLastPension - pensionsPerPage;

  //     
  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };
  const [searchTerm, setSearchTerm] = useState('');
  const [searchResult2, setSearchResult2] = useState([]);

  const currentPensions = searchResult2.slice(
    indexFirstPension,
    indexOfLastPension
  );

  const location = useLocation();

  //  
  const inputValue = location.state?.searchTerm.term;
  //  
  const [inputcheckinDate, setInputcheckinDate] = useState(
    location.state?.searchTerm.checkindate
  );
  //  
  const [inputcheckoutDate, setInputcheckoutDate] = useState(
    location.state?.searchTerm.checkoutdate
  );
  // 
  const [peopleNumber, setPeopleNumber] = useState(
    location.state?.searchTerm.peopleNumber
  );

  console.log(
    ' : ' + inputValue + '  : ' + inputcheckinDate,
    '  : ' + inputcheckoutDate + ' : ' + peopleNumber
  );

  //  
  const urlParams = new URLSearchParams(window.location.search);
  const selectedRegion = urlParams.get('region');

  //  
  const [filterButton, setFilterButton] = useState('');

  const navigate = useNavigate();
  //   id 
  const handleDetailPage = (id, name) => {
    const selectedId = id;

    const pensionName = name;
    navigate('/DetailsPage', {
      state: {
        inputcheckinDate,
        inputcheckoutDate,
        peopleNumber,
        selectedId,
        pensionName,
      },
    });
  };

  //   setSearch !!!!
  useEffect(() => {
    setSearchTerm(selectedRegion);
  }, [selectedRegion]);

  //   setSearch !!!!
  useEffect(() => {
    if (inputValue !== null && selectedRegion === null) {
      setSearchTerm(inputValue);
    }
  }, []);

  //searchTerm     null!!!    
  useEffect(() => {
    if (searchTerm !== '') {
      handleSearch();
    }
  }, [searchTerm]);

  const handleSearch = async () => {
    try {
      const response = await axios.get(
        `http://localhost:8282/penpick/searchAll`,
        {
          params: {
            term: searchTerm,
            filter: filterButton,
          },
        }
      );

      console.log(response.data);

      if (response.data.length === 0) {
        alert('   .');
      } else {
        console.log(response.data);

        const responseData = Array.isArray(response.data)
          ? response.data
          : [response.data];

        setSearchResult2(responseData);
      }
    } catch (error) {
      console.error('Error searching users:', error);
      setSearchResult2([]);
    }
  };

  const handleSearch2 = async (e) => {
    e.preventDefault();
    handleSearch();
  };

  const handleFilter = (filter) => {
    if (filter === 1) {
      setFilterButton('');
      handleSearch();
      console.log('');
    } else if (filter === 2) {
      setFilterButton('');
      handleSearch();
      console.log('');
    } else if (filter === 3) {
      console.log('');
      setFilterButton('');
      handleSearch();
    } else if (filter === 4) {
      console.log('');
      setFilterButton('');
      handleSearch();
    } else if (filter === 5) {
      console.log('');
      setFilterButton('');
      handleSearch();
    } else if (filter === 6) {
      console.log('');
      setFilterButton('');
      handleSearch();
    } else if (filter === 7) {
      console.log('');
      setFilterButton('');
      handleSearch();
    } else if (filter === 8) {
      console.log('');
      setFilterButton('');
      handleSearch();
    }
  };

  //  
  const [loading, setLoading] = useState(true);
  const [images, setImages] = useState([]);
  useEffect(() => {
    const fetchImages = async () => {
      try {
        const response = await fetch('http://localhost:8282/all');
        if (!response.ok) {
          throw new Error('  .');
        }
        const imagesData = await response.json();
        setImages(imagesData);
      } catch (error) {
        console.error(' .:', error);
      } finally {
        setLoading(false);
      }
    };

    fetchImages();
  }, []);

  return (
    <div id='AllContain'>
      <div id='HeaderBannerImg'>
        <div id='PensionListCenterBox'>
          <nav id='HeaderNav' class='navbar navbar-expand-lg '>
            <div class='container-fluid'>
              <button
                class='navbar-toggler'
                type='button'
                data-bs-toggle='collapse'
                data-bs-target='#navbarSupportedContent'
                aria-controls='navbarSupportedContent'
                aria-expanded='false'
                aria-label='Toggle navigation'
              >
                <span class='navbar-toggler-icon'></span>
              </button>
              <div class='collapse navbar-collapse' id='navbarSupportedContent'>
                <ul class='navbar-nav me-auto mb-2 mb-lg-0'>
                  <a
                    class='nav-link active'
                    aria-current='page'
                    id='HeaderQnALink'
                    href='/QnA'
                  >
                    
                  </a>
                  <a
                    class='nav-link active'
                    aria-current='page'
                    id='HeaderEventLink'
                    href='/EventPage'
                  >
                    
                  </a>
                  <li class='nav-item'>
                    <a
                      id='HeaderCartImg'
                      class='nav-link active'
                      aria-current='page'
                      href='/PrevFresh'
                    >
                      
                    </a>
                  </li>

                  <li class='nav-item dropdown'>
                    <a
                      class='nav-link '
                      href='/'
                      role='button'
                      data-bs-toggle='dropdown'
                      aria-expanded='false'
                    >
                      <img id='HeaderUserImg' src={UserImg} alt='' />
                    </a>
                    <ul class='dropdown-menu' id='HeaderDropdownBox'>
                      <li>
                        <a
                          id='HeaderDropDownLink'
                          class='dropdown-item'
                          href='/'
                        >
                          /
                        </a>
                      </li>
                      <li>
                        <a
                          id='HeaderDropDownLink'
                          class='dropdown-item'
                          href='#'
                        >
                           
                        </a>
                      </li>
                      <li>
                        <a
                          class='dropdown-item'
                          id='HeaderDropDownLinkMYPAGE'
                          href='/Mypage'
                        >
                          
                        </a>
                      </li>
                    </ul>
                  </li>
                </ul>
              </div>
            </div>
          </nav>
          {/*   */}
          <div id='HeaderBannerLink'>
            <a href='/' id='HeaderMainLink'>
              <img id='HeaderPenPickImg' src={PenPickLogo} alt=''></img>
            </a>
            <form id='PensionSearchForm'>
              <input
                id='PensionInput'
                type='text'
                placeholder=' '
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
              />
              <span id='InputBar'>|</span>
              {/*    */}
              <input
                id='HeaderPensionInputDate'
                value={inputcheckinDate}
                min={inputcheckinDate}
                max={oneMonthLaterYear + '-' + oneMonthLaterMonth + '-' + day}
                type='date'
                onChange={(e) => setInputcheckinDate(e.target.value)}
              />
              <span id='InputBar'>|</span>
              {/*    */}
              <input
                id='HeaderPensionInputDate'
                min={inputcheckinDate}
                value={inputcheckoutDate}
                max={
                  oneMonthLaterYear +
                  '-' +
                  oneMonthLaterMonth +
                  '-' +
                  nextDayOfMonth
                }
                type='date'
                onChange={(e) => setInputcheckoutDate(e.target.value)}
              />
              <span id='InputBar'>|</span>
              {/*    */}
              <input
                id='HeaderPensionInputNumber'
                value={peopleNumber}
                type='number'
                onChange={(e) => setPeopleNumber(e.target.value)}
                min={1}
                max={6}
              />
              
              <button
                id='PensionSearchButton'
                type='submit'
                onClick={handleSearch2}
              >
                <img id='PensionSearchImg' src={SearchButton} alt='' />
              </button>
            </form>
          </div>
        </div>
      </div>

      <div id='container' className='row'>
        <div id='bigfirstbox' className='col-md-3'>
          <div id='firstBox'>
            <h3>{searchTerm}   </h3>
            <button
              id='pensionListMapLink'
              onClick={() =>
                navigate('/PesionMap', {
                  state: {
                    searchTerm: searchTerm,
                  },
                })
              }
            >
              <img src={MapImg} alt='' id='MapImg'></img>
            </button>
            <div id='filter'>
              <h6 id='filterTitle'></h6>
              <form>
                <hr id='hrfilter' />
                <div>
                  <h6 id='filterTitle'></h6>
                  <button
                    id='filterButton'
                    onClick={() => handleFilter(1)}
                    type='button'
                  >
                    
                  </button>
                  <button
                    id='filterButton'
                    onClick={() => handleFilter(2)}
                    type='button'
                  >
                    
                  </button>
                  <button
                    id='filterButton'
                    onClick={() => handleFilter(3)}
                    type='button'
                  >
                    
                  </button>
                  <br />
                  <button
                    id='filterButton'
                    onClick={() => handleFilter(4)}
                    type='button'
                  >
                    {' '}
                    
                  </button>
                  <button
                    id='filterButton'
                    onClick={() => handleFilter(5)}
                    type='button'
                  >
                    
                  </button>
                  <button
                    id='filterButton'
                    onClick={() => handleFilter(6)}
                    type='button'
                  >
                    
                  </button>
                  <br />
                  <button
                    id='filterButton'
                    onClick={() => handleFilter(7)}
                    type='button'
                  >
                    
                  </button>
                  <button
                    id='filterButton'
                    onClick={() => handleFilter(8)}
                    type='button'
                  >
                    
                  </button>

                  {filterButton !== '' && (
                    <div>
                      <p id='CheckfilterTitle'> </p>
                      <button id='Checkfilter'>
                        {filterButton}
                        <span id='pensionListXbutton'>X</span>
                      </button>
                    </div>
                  )}

                  <hr id='hrfilter' />
                </div>
              </form>
            </div>
          </div>
        </div>
        <div className='col-md-8'>
          <div id='PensionSearchList'>
            <ul>
              {loading ? (
                //      
                <div class='d-flex justify-content-center'>
                  <div
                    class='spinner-border'
                    role='status'
                    id='pensionListLoading'
                  >
                    <span class='visually-hidden'>Loading...</span>
                  </div>
                </div>
              ) : (
                currentPensions.map((pension) => (
                  <div id='pensionBox' key={pension.id}>
                    <ul>
                      {/*      */}
                      {images.map(
                        (image, index) =>
                          image &&
                          image.imageData &&
                          pension.name === image.name && (
                            <img
                              onClick={() =>
                                handleDetailPage(pension.id, pension.name)
                              }
                              key={index}
                              src={`data:image/jpeg;base64,${image.imageData}`}
                              id='pensionSearchImg'
                              alt={image.imageName}
                            />
                          )
                      )}
                      {/*       */}
                      {images.every((image) => pension.name !== image.name) && (
                        <img
                          onClick={() =>
                            handleDetailPage(pension.id, pension.name)
                          }
                          id='pensionSearchImg'
                          src={NoImg}
                          alt=' .'
                        />
                      )}
                    </ul>
                    <div id='pensionDescription'>
                      <p
                        id='pensionName'
                        onClick={() =>
                          handleDetailPage(pension.id, pension.name)
                        }
                      >
                        {pension.name}
                      </p>
                      <p>{pension.address}</p>
                      {pension.check_in} - {pension.check_out}
                    </div>
                    <hr id='PensionSearchListhr' />
                  </div>
                ))
              )}
            </ul>
            <br />
            <br />
            <div id='paginationButtonBox'>
              <Pagination>
                {[
                  ...Array(
                    Math.ceil(searchResult2.length / pensionsPerPage)
                  ).keys(),
                ].map((number) => (
                  <Pagination.Item
                    key={number + 1}
                    active={number + 1 === currentPage}
                    onClick={() => handlePageChange(number + 1)}
                    id='pensionlistpageButton'
                  >
                    <span id='otherPageButton'> {number + 1}</span>
                  </Pagination.Item>
                ))}
              </Pagination>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
export default PensionList;
