import React, { useEffect, useState } from "react";
import axios from "axios"; // axios import.
import Header from "./Header";
import "../css/WriteQuestion.css";
import "bootstrap/dist/css/bootstrap.min.css";

export default function WriteQuestion() {
  const [nickname, setNickname] = useState("");
  const [title, setTitle] = useState("");
  const [content, setContent] = useState("");
  //   const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const fetchUserData = async () => {
      try {
        const res = await axios.get("http://localhost:8282/userdata", {
          withCredentials: true,
        });
        setNickname(res.data.nickname);
      } catch (err) {
        console.error("   ", err);
      }
    };
    fetchUserData();
  }, []);

  const handleSubmit = async (e) => {
    e.preventDefault(); //      

    try {
      const res = await axios.post("http://localhost:8282/QnA/write-question", {
        nickname: nickname,
        questionTitle: title,
        questionContent: content,
        writtenDate: new Date(),
      });
      console.log(res.data);
      alert(" .");
      window.location.href = "/QnA";
    } catch (err) {
      console.error("  ", err);
      alert("  .");
    }
  };

  return (
    <div>
      <Header />
      <div id="write-QnA-header">
        <h2>
          <strong> </strong>
        </h2>
        <p id="QnA-description">   ?</p>
      </div>
      <br />
      <div id="write-QnA-container">
        <div id="write-question-form">
          <h4>
            <b>  </b>
          </h4>
          <br />
          <form onSubmit={handleSubmit}>
            <div>
              <label></label>
              <br />
              <input
                type="text"
                value={title}
                onChange={(e) => setTitle(e.target.value)}
                required
                className="form-control"
              />
            </div>
            <br />
            <div>
              <label></label>
              <br />
              <textarea
                value={content}
                onChange={(e) => setContent(e.target.value)}
                required
                className="form-control"
                style={{ height: "250px" }}
              />
            </div>
            <br />
            <button
              type="submit"
              className="btn primary"
              style={{ float: "right" }}
            >
              
            </button>
          </form>
        </div>
      </div>
    </div>
  );
}
