import React from 'react';
import logo from '../img/.png';

function Footer() {
  return (
    <div id='FooterContainer'>
      <div id='footer'>
        <div id='FooterBox'>
          <img id='footerlogo' src={logo} alt='logo' />
          <p id='Footercopylight'>
            &copy; ()penpick   |  
          </p>
          <p id='FooterAdress'> |   </p>
          <p id='FooterAdress'> | help@penpick.kr</p>
        </div>
      </div>
    </div>
  );
}

export default Footer;
