import Stomp from 'webstomp-client';
import React, { useState, useEffect } from 'react';
import SockJS from 'sockjs-client';
import '../css/PensionMainPage.css';
import user from '../img/userImg.png';
import axios from 'axios';

const Chat = () => {
  //     
  const [messages, setMessages] = useState([]);

  // Stomp    
  const [stompClient, setStompClient] = useState(null);

  //      
  const [inputMessage, setInputMessage] = useState('');

  const [anonymousMessage, setAnonymousMessage] = useState([]);

  const [myClientId, setMyClientId] = useState(null);

  //  
  const [nickname, setNickName] = useState('');
  useEffect(() => {
    const fetchUserData = async () => {
      try {
        const res = await axios.get('http://localhost:8282/userdata', {
          withCredentials: true,
        });

        const name = res.data.nickname;
        setNickName(name);
        console.log(nickname + 'or' + name);
      } catch (err) {
        console.error('   ', err);
      }
    };
    fetchUserData();
  }, []);

  //    WebSocket     
  useEffect(() => {
    const connect = () => {
      const socket = new SockJS('http://localhost:8282/websocket');
      console.log(' ');
      const stomp = Stomp.over(socket);
      console.log(' ');

      if (socket.readyState !== 1) {
        stomp.connect({}, (frame) => {
          console.log(': ' + frame);
          setStompClient(stomp);
          // const message = {
          //   clientId: 'stompClient.sessionId',
          // };

          // stomp.send('/app/connect', JSON.stringify({ message }));
          // stomp.subscribe('/topic/myClientId', (response) => {
          //   const newClientId = JSON.parse(response.body);
          //   setMyClientId(newClientId);
          // });
        });
      }

      return () => {
        socket.close();
      };
    };

    connect();
  }, []);

  const sendMessage = (content) => {
    if (stompClient && stompClient.connected) {
      const message = {
        content: content,
        sender: nickname,
        direction: 'sent',
        senderClientId: stompClient.sessionId,
      };
      //  '/app/chat'   
      stompClient.send('/app/websocket', JSON.stringify(message));
      console.log(JSON.stringify(message));

      setInputMessage('');
    } else {
      console.error('Stomp client is not initialized.');
    }
  };

  // '/topic/messages'    
  const subscribeToMessages = () => {
    // '/topic/messages'  
    stompClient.subscribe('/topic/messages', (response) => {
      //     
      const message = JSON.parse(response.body);
      message.direction = 'received';
      setMessages((prevMessages) => [...prevMessages, message]);
    });
  };

  // Stomp       
  useEffect(() => {
    if (stompClient) {
      subscribeToMessages();
    }
  }, [stompClient]);

  const now = new Date();
  const hours = now.getHours();
  const minutes = now.getMinutes();
  const year = now.getFullYear();
  var month = ('0' + (now.getMonth() + 1)).slice(-2);
  var day = ('0' + now.getDate()).slice(-2);

  //input 
  //

  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      sendMessage(inputMessage); // const  
    }
  };

  return (
    <div>
      <div id='ChatContainer'>
        <div id='firstChatbox'>
          <p id='ChatInformation'>
            {year}{month}{day}
          </p>
          <p id='ChatInformation' class='chatinfo'>
             .
          </p>

          {messages.map((message, index) => (
            <div
              id='userInputMessage'
              key={index}
              className={
                message.sender === nickname ? 'sentByUser1' : 'sentByUser2'
              }
            >
              <div></div>
              <img
                className={
                  message.sender === nickname
                    ? 'MessageUseImg1'
                    : 'MessageUseImg2'
                }
                id='MessageUseImg'
                src={user}
                alt=''
              ></img>
              <span
                className={
                  message.sender === nickname
                    ? 'messageContent1'
                    : 'messageContent2'
                }
                id='messageContent'
              >
                {message.content}
              </span>
              <span
                className={
                  message.sender === nickname ? 'currentTime1' : 'currentTime2'
                }
                id='currentTime'
              >
                {hours}:{minutes}
              </span>
              <p id={message.sender === nickname ? 'chatuser1' : 'chatuser2'}>
                {message.sender}
              </p>
            </div>
          ))}
        </div>
        <div id='messageInput'>
          <input
            id='messageInputBox'
            type='text'
            onKeyPress={handleKeyPress}
            onChange={(e) => setInputMessage(e.target.value)}
            value={inputMessage}
            maxLength={'15'}
            placeholder='  '
          />
          <button id='ChatSendButton' onClick={() => sendMessage(inputMessage)}>
            
          </button>
        </div>
      </div>
    </div>
  );
};

export default Chat;
