import React, { useEffect, useState } from 'react';
import Header from './Header';
import '../css/EventPage.css';
import Calendar from '../img/.png';
import UserImg from '../img/.png';
import axios from 'axios';
import 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import { useLocation } from 'react-router';
import event from '../img/EventImg1.jpg';
import eventThumnail from '../img/ .png';
import eventThumnai2 from '../img/ 2.png';
import eventThumnai3 from '../img/ 3.png';

function EventPage() {
  const [contentList, setContentList] = useState({ comment: '', content: '' });
  const [saveList, setSaveList] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const location = useLocation();

  const currentURL = location.pathname;

  const handleMovePage = (title) => {
    const content = title;
    window.location.href = `EventDetail?content=${content}`;
  };

  useEffect(() => {
    handleEventList();
  }, []);

  const handleEventList = async () => {
    try {
      const response = await axios.get(`http://localhost:8282/event/eventList`);

      setSaveList(response.data);
    } catch (error) {
      console.error('    ' + error);
    }
  };

  const imagePaths = {
    1: require('../img/ .png'),
    2: require('../img/ 2.png'),
    3: require('../img/ 3.png'),
  };

  return (
    <div>
      <Header />
      <div id='container'>
        <div id='eventContainer'>
          <div id='EventCategory'>
            <h5 id='eventcategory'></h5>
            {currentURL === 'eventTitle'}
            <a id='eventlink1' href='eventPage'>
              
            </a>
            <br></br>
            <a href='GameLand' id='gamelandlink1'>
              
            </a>
          </div>
          <div id='EventMain'>
            <a id='eventMainTitle' href='EventPage'>
              
            </a>

            <h6 id='EventContent'>
                !   !
            </h6>

            <div id='eventBox'>
              <div id='eventthumnailBox'>
                <a id='EventPostLink' href='/EventDetail?content=1'>
                  <img
                    id='EventPostImg'
                    src={eventThumnail}
                    alt=''
                  />
                  <span id='eventlinkdes'>    </span>
                </a>
              </div>
              <div id='eventthumnailBox'>
                <a id='EventPostLink' href='/EventDetail?content=2'>
                  <img
                    id='EventPostImg'
                    src={eventThumnai2}
                    alt=''
                  />
                  <span id='eventlinkdes'>
                     50%     
                  </span>
                </a>
              </div>
              <div id='eventthumnailBox'>
                <a id='EventPostLink' href='/EventDetail?content=3'>
                  <img
                    id='EventPostImg'
                    src={eventThumnai3}
                    alt=''
                  />
                  <span id='eventlinkdes'>
                         !
                  </span>
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default EventPage;
