import '../css/PensionMainPage.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import PenPickLogo from '../img/PenPickLogo.png';
import EventImg from '../img/EventImg1.jpg';
import FormImg from '../img/.png';
import React, { useState, useEffect } from 'react';
import CartImg from '../img/.png';
import Header from './Header';
import upImg from '../img/.png';
import {
  Button,
  Container,
  Row,
  Col,
  InputGroup,
  FormControl,
  Nav,
} from 'react-bootstrap';
import pensionImg1 from '../img/JS_2_3.jpg';
import pensionImg2 from '../img/_2_3.jpg';
import pensionImg3 from '../img/_3_3.jpg';
import pensionImg4 from '../img/_5_3.jpg';
import BestLocationImg1 from '../img/.jpg';
import BestLocationImg2 from '../img/.jpg';
import BestLocationImg3 from '../img/.jpg';
import BestLocationImg4 from '../img/.jpg';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import PensionList from './PensionList';
import Chat from './Chat';
import imgChat from '../img/.png';
import marker from '../img/2.png';
import event1 from '../img/ .png';
import event2 from '../img/ 2.png';
import event3 from '../img/ 3.png';

function PensionMainPage() {
  //  !
  const [loginUser, setLoginUser] = useState('');
  useEffect(() => {
    const fetchUserData = async () => {
      try {
        const res = await axios.get('http://localhost:8282/userdata', {
          withCredentials: true,
        });

        const name = res.data.nickname;
        setLoginUser(name);
        console.log('   :' + loginUser);
      } catch (err) {
        console.error('   ', err);
      }
    };
    fetchUserData();
  }, []);
  // 
  const now = new Date();
  const year = now.getFullYear();
  const month = ('0' + (now.getMonth() + 1)).slice(-2);
  const day = ('0' + now.getDate()).slice(-2);

  //   
  const nextDay = new Date(now);
  nextDay.setDate(nextDay.getDate() + 1);
  const nextDayOfMonth = ('0' + nextDay.getDate()).slice(-2);
  const Month = ('0' + (nextDay.getMonth() + 1)).slice(-2);
  const nextYear = nextDay.getFullYear();

  //     
  const oneMonthLater = new Date(now);
  oneMonthLater.setMonth(oneMonthLater.getMonth() + 1);
  const oneMonthLaterYear = oneMonthLater.getFullYear();
  const oneMonthLaterMonth = ('0' + (oneMonthLater.getMonth() + 1)).slice(-2);
  const oneMonthLaterDay = ('0' + oneMonthLater.getDate()).slice(-2);

  // 
  const [searchTerm, setSearchTerm] = useState({
    term: '',
    checkindate: year + '-' + month + '-' + day,
    checkoutdate: nextYear + '-' + Month + '-' + nextDayOfMonth,
    peopleNumber: '2',
  });
  //    
  const navigate = useNavigate();
  const handleSearch = () => {
    navigate('/PensionList', {
      state: {
        searchTerm,
      },
    });
  };

  //   
  const selectRegionAndNavigate = (region) => {
    const selectedRegion = region;
    window.location.href = `/PensionList?region=${selectedRegion}`;
  };

  //   
  const onPopup = () => {
    const url = 'Chat';
    window.open(url, '_blank', 'width=750px,height=890px');
  };
  const onPopuphandler = () => {
    if (loginUser === '') {
      alert('   .');
    } else {
      onPopup();
    }
  };

  const Up = () => {
    window.scrollTo(0, 0);
  };

  return (
    <div id='mainpageContainer'>
      <Header />

      <div id='BannerImg1'>
        {/*   */}
        <div id='MainTitleBox'>
          <h1 id='MainTitle' class='fade-in-element'>
            <span id='firstmt'>
              <span id='mt1'> </span> <span id='mt2'></span>
            </span>
            <br />
            <span id='mt3'></span> <span id='mt4'></span>
            <span>
              <img id='maintitlemarker' src={marker} alt=''></img>
            </span>
          </h1>
        </div>

        {/*     */}
        <div id='SearchForm'>
          <div id='form_Img_Title'>
            <img id='formImg' src={FormImg} alt='' />
            <span id='formTitle'></span>
          </div>
          {/*   */}
          <form className='row'>
            <div id='SearchFormBar'></div>
            {/*  */}
            <input
              type='text'
              id='input1'
              className='form-control col-md-3'
              placeholder=' '
              value={searchTerm.term}
              onChange={(e) =>
                setSearchTerm((prevState) => ({
                  ...prevState,
                  term: e.target.value,
                }))
              }
            />
            {/*   */}
            <input
              id='input3'
              className='form-control col-md-3'
              type='date'
              placeholder=''
              value={searchTerm.checkindate}
              min={year + '-' + month + '-' + day}
              max={oneMonthLaterYear + '-' + oneMonthLaterMonth + '-' + day}
              onChange={(e) =>
                setSearchTerm((prevState) => ({
                  ...prevState,
                  checkindate: e.target.value,
                }))
              }
            />
            {/*   */}
            <input
              id='input3'
              className='form-control col-md-3'
              type='date'
              placeholder=''
              value={searchTerm.checkoutdate}
              min={searchTerm.checkindate}
              max={
                oneMonthLaterYear +
                '-' +
                oneMonthLaterMonth +
                '-' +
                nextDayOfMonth
              }
              onChange={(e) =>
                setSearchTerm((prevState) => ({
                  ...prevState,
                  checkoutdate: e.target.value,
                }))
              }
            />
            {/*  */}
            <input
              min={1}
              max={6}
              id='input2'
              className='form-control col-md-3'
              placeholder=''
              type='number'
              value={searchTerm.peopleNumber}
              onChange={(e) =>
                setSearchTerm((prevState) => ({
                  ...prevState,
                  peopleNumber: e.target.value,
                }))
              }
            />
            {/*   */}
            <button
              id='MainsearchButton'
              className='btn col-md-3'
              onClick={(e) => {
                e.preventDefault();
                handleSearch();
              }}
            >
              search
            </button>
          </form>
        </div>
      </div>

      <div id='mainContainer'>
        <img
          id='ChatImg'
          onClick={onPopuphandler}
          src={imgChat}
          alt=''
        />
        <img id='Upimg' onClick={Up} src={upImg} alt='' />

        {/*   */}
        <div id='Event'>
          <h5 id='EventTitle'></h5>
          <div id='EventImgBox'>
            <a id='EventLink' href='/EventDetail?content=1'>
              <img id='EventImg' src={event1} alt='' />
            </a>
            <a id='EventLink' href='/EventDetail?content=2'>
              <img id='EventImg' src={event2} alt='' />
            </a>
            <a id='EventLink' href='/EventDetail?content=3'>
              <img id='EventImg' src={event3} alt='' />
            </a>
          </div>
        </div>

        <div id='PopularPensionList'>
          <span id='PopularPensionTitle'> </span>
          <span id='description'>    </span>
          <br />
          <div id='LinkImgBox' className='row'>
            <div className='col-md-3' id='popularPensionGroup'>
              <a
                href='http://localhost:3000/PensionList?region='
                alt=''
              >
                <img id='pensionImg' src={pensionImg1} alt='pensionImg'></img>
                <h5 id='MainPopularLocationName'>  </h5>
              </a>
            </div>
            <div className='col-md-3' id='popularPensionGroup'>
              <a
                href='http://localhost:3000/PensionList?region= '
                alt=''
              >
                <img id='pensionImg' src={pensionImg2} alt='pensionImg'></img>
                <h5 id='MainPopularLocationName'> </h5>
              </a>
            </div>
            <div className='col-md-3' id='popularPensionGroup'>
              <a
                href='http://localhost:3000/PensionList?region= '
                alt=''
              >
                <img id='pensionImg' src={pensionImg3} alt='pensionImg'></img>
                <h5 id='MainPopularLocationName'> </h5>
              </a>
            </div>
            <div className='col-md-3' id='popularPensionGroup'>
              <a
                href='http://localhost:3000/PensionList?region='
                alt=''
              >
                <img id='pensionImg' src={pensionImg4} alt='pensionImg'></img>
                <h5 id='MainPopularLocationName'> </h5>
              </a>
            </div>
          </div>
        </div>

        <div id='PopularLocation'>
          <span id='PopularLocationTitle'> </span>
          <span id='description'>    </span>
          <br />
          <div id='LinkImgBox'>
            <button
              id='MainPopularLocationButton'
              className='col-md-2'
              onClick={() => selectRegionAndNavigate('')}
            >
              <img
                id='pensionImg'
                src={BestLocationImg1}
                alt='pensionImg'
              ></img>
              <h5 id='MainPopularLocationName'></h5>
            </button>
            <button
              id='MainPopularLocationButton'
              className='col-md-2'
              onClick={() => selectRegionAndNavigate('')}
            >
              <img
                id='pensionImg'
                src={BestLocationImg4}
                alt='pensionImg'
              ></img>
              <h5 id='MainPopularLocationName'></h5>
            </button>
            <button
              id='MainPopularLocationButton'
              className='col-md-2'
              onClick={() => selectRegionAndNavigate('')}
            >
              <img
                id='pensionImg'
                src={BestLocationImg3}
                alt='pensionImg'
              ></img>
              <h5 id='MainPopularLocationName'></h5>
            </button>
            <button
              id='MainPopularLocationButton'
              className='col-md-2'
              onClick={() => selectRegionAndNavigate('')}
            >
              <img
                id='pensionImg'
                src={BestLocationImg2}
                alt='pensionImg'
              ></img>
              <h5 id='MainPopularLocationName'></h5>
            </button>
          </div>
        </div>

        <div id='LocationList'>
          <div id='BigLocationList'>
            <span id='PopularLocationTitle'>  </span>
            <span id='BigLocationListdescription'>
                 ?
            </span>
          </div>
          <div id='MainLocationLinkBox' className='row'>
            <button
              id='MainLocationButton'
              className='col-md-2'
              onClick={() => selectRegionAndNavigate('')}
            >
              
            </button>
            <button
              id='MainLocationButton'
              className='col-md-2'
              onClick={() => selectRegionAndNavigate('')}
            >
              
            </button>
            <button
              id='MainLocationButton'
              className='col-md-2'
              onClick={() => selectRegionAndNavigate('')}
            >
              
            </button>
            <button
              id='MainLocationButton'
              className='col-md-2'
              onClick={() => selectRegionAndNavigate('')}
            >
              
            </button>
            <button
              id='MainLocationButton'
              className='col-md-2'
              onClick={() => selectRegionAndNavigate('')}
            >
              
            </button>
          </div>
          <div id='MainLocationLinkBox' className='row'>
            <button
              id='MainLocationButton'
              className='col-md-3'
              onClick={() => selectRegionAndNavigate('')}
            >
              
            </button>
            <button
              id='MainLocationButton'
              className='col-md-3'
              onClick={() => selectRegionAndNavigate('')}
            >
              
            </button>
            <button
              id='MainLocationButton'
              className='col-md-3'
              onClick={() => selectRegionAndNavigate('')}
            >
              
            </button>
            <button
              id='MainLocationButton'
              className='col-md-3'
              onClick={() => selectRegionAndNavigate('')}
            >
              
            </button>
            <button
              id='MainLocationButton'
              className='col-md-3'
              onClick={() => selectRegionAndNavigate('')}
            >
              
            </button>
          </div>
          <div id='MainLocationLinkBox' className='row'>
            <button
              id='MainLocationButton'
              className='col-md-2'
              onClick={() => selectRegionAndNavigate('')}
            >
              
            </button>
            <button
              id='MainLocationButton'
              className='col-md-2'
              onClick={() => selectRegionAndNavigate('')}
            >
              
            </button>
            <button
              id='MainLocationButton'
              className='col-md-2'
              onClick={() => selectRegionAndNavigate('')}
            >
              
            </button>
            <button
              id='MainLocationButton'
              className='col-md-2'
              onClick={() => selectRegionAndNavigate('')}
            >
              
            </button>
            <button
              id='MainLocationButton'
              className='col-md-2'
              onClick={() => selectRegionAndNavigate('')}
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
export default PensionMainPage;
