import React, { useState } from "react";
import "../css/Login.css";
import "../css/ResetPassword.css";
import axios from "axios";
import "bootstrap/dist/css/bootstrap.min.css";
import Header from "./Header";

export default function ResetPassword() {
  const [email, setEmail] = useState("");
  const [emailErrorMessage, setEmailErrorMessage] = useState("");

  //        
  const [loading, setLoading] = useState(false);

  const isEmailValid = (email) => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
  };

  const handleEmailChange = (e) => {
    const newEmail = e.target.value;
    setEmail(newEmail);
    //   
    if (!isEmailValid(newEmail)) {
      setEmailErrorMessage("   ");
    } else {
      setEmailErrorMessage("");
    }
  };

  const handleResetPassword = async () => {
    try {
      setLoading(true); //       

      //   
      if (!isEmailValid(email)) {
        alert("   ");
        return;
      }

      //   
      const response = await axios.post(
        "http://localhost:8282/resetPassword",
        {
          email,
        },
        {
          withCredentials: true,
        }
      );

      alert(response.data); //       
      window.location.href = "/";
    } catch (error) {
      console.error("  ", error);
      alert("  .");
    } finally {
      setLoading(false); //  
    }
  };

  return (
    <div>
      <Header />
      <div className="main">
        <form id="loginForm" method="get" action="/login">
          <p id="restePassword-title">
            <strong> </strong>
          </p>
          <p id="resetPassword-message">
                  .
          </p>
          <br />
          <div style={{ marginTop: "40px" }}>
            <label id="resetPassword-email-label"></label>
            <div className="dot-badge"></div>
          </div>
          <input
            id="emailInput"
            type="email"
            className="form-control"
            placeholder="yourEmail@penpick.co.kr"
            name="input_id"
            value={email}
            onChange={handleEmailChange}
          />
          {emailErrorMessage && (
            <span
              style={{
                float: "right",
                marginRight: "3px",
                marginTop: "5px",
                color: "red",
                fontSize: "small",
              }}
            >
              {emailErrorMessage}
            </span>
          )}
          <br />
          <br />
          <button
            id="resetPassword-button"
            type="button"
            onClick={handleResetPassword}
            disabled={loading || !email.trim() || !isEmailValid(email)}
            className={`reset-button ${loading ? "loading" : ""}`}
          >
            {loading ? " " : ""}
          </button>
        </form>
      </div>
    </div>
  );
}
