import React, { useState, useEffect } from "react";
import axios from "axios";
import { useLocation, useNavigate } from "react-router-dom";
import "../css/reset-css.css";
import "../css/item.css";
import Header from "./Header";
import fresh1 from "../img/.png";
import fresh2 from "../img/.png";
import fresh3 from "../img/.png";
import fresh4 from "../img/.jpg";
import fresh5 from "../img/.jpg";
import fresh6 from "../img/.jpg";
import fresh7 from "../img/.jpg";
import fresh8 from "../img/.jpg";

export default function FreshHome() {
  const navigate = useNavigate();
  //PrevFresh useNavigate  
  const location = useLocation();
  const resnum = location.state.id;
  console.log("", resnum);
  //    item_num
  const [selectedValues, setSelectedValues] = useState([]);
  //   
  const [inputItemCount, setItemCount] = useState({});
  //    
  const [totalAmount, setTotalAmount] = useState(0);
  //  
  const [orderList, setOrderList] = useState([]);
  // 
  const [User, setUser] = useState([]);
  // 
  const [freshitems, setFreshItems] = useState([]);
  //   
  const [agreedToTerms, setAgreedToTerms] = useState(false);

  // 
  // resnum    sessionStorage  
  useEffect(() => {
    window.sessionStorage.setItem("id", resnum);
  }, [resnum]);

  //  
  useEffect(() => {
    window.sessionStorage.setItem("totalprice", totalAmount);
  }, [totalAmount]);
  console.log("", totalAmount);

  //  
  useEffect(() => {
    const fetchItemData = async () => {
      try {
        // const response = await axios.get(
        //   "http://localhost:8282/productItem/list"
        // );
        const tmpData = [
          { itemnum: 1, itemName: " 150g", itemPrice: 12000 },
          { itemnum: 2, itemName: " 150g", itemPrice: 11000 },
          { itemnum: 3, itemName: "", itemPrice: 2000 },
          { itemnum: 5, itemName: "", itemPrice: 2000 },
          { itemnum: 6, itemName: "", itemPrice: 2000 },
          { itemnum: 7, itemName: "", itemPrice: 1000 },
          { itemnum: 8, itemName: "", itemPrice: 1500 },
          { itemnum: 9, itemName: "", itemPrice: 10000 },
        ];
        // setfreshitems(response.data);
        setFreshItems(tmpData);
        // console.log(response.data);
      } catch (error) {
        console.log("  .", error);
      }
    };
    fetchItemData();
  }, []);

  //   
  useEffect(() => {
    //         ( )
    const fetchUserCartData = async () => {
      try {
        const res = await axios.get("http://localhost:8282/userdata", {
          withCredentials: true,
        });
        setUser(res.data);
      } catch (err) {
        console.error("   ", err);
      }
    };
    fetchUserCartData();
  }, []);

  // ,  ,       order  .
  useEffect(() => {
    const selectedItems = selectedValues
      .filter((itemnum) => {
        return Number(inputItemCount?.[itemnum]);
      })
      .map((itemnum) => {
        const count = inputItemCount?.[itemnum] || 1;
        const targetItem =
          freshitems.find((item) => String(item?.itemnum) === itemnum) || {};
        return {
          ...targetItem,
          user_id: User.id,
          resnum: { id: resnum },
          itemCount: count,
          item_Total_Price: count * Number(targetItem.itemPrice),
        };
      });
    //    
    setOrderList(selectedItems);
    const totalAmountArr = selectedItems.map((item) => item?.item_Total_Price);
    if (totalAmountArr?.length > 0) {
      const totalAmount = totalAmountArr.reduce((a, b) => a + b);
      setTotalAmount(totalAmount);
    }
  }, [selectedValues, inputItemCount, freshitems, resnum]);

  // (   )
  const registerAddOrder = async () => {
    if (!agreedToTerms.terms1 || !agreedToTerms.terms2) {
      alert("    .");
      return;
    }

    try {
      const jobs = orderList.map((targetItem) => {
        console.log(targetItem);
        return axios.post("http://localhost:8282/freshorder/add", targetItem, {
          withCredentials: true,
        });
      });
      console.log(orderList);
      const result = await Promise.all(jobs);
      navigate("/sandbox", {
        replace: true,
      });
      console.log(result);
    } catch (error) {
      console.error(" ", error);
    }
  };
  //  
  const handleCheckboxChange = (event) => {
    const currentItemNum = event.target.value;
    // 
    const isChecked = event.target.checked;
    // 
    if (isChecked) {
      setSelectedValues([...selectedValues, currentItemNum]);
      if (!inputItemCount?.[currentItemNum]) {
        setItemCount({ ...inputItemCount, [currentItemNum]: 1 });
      }
      // 
    } else {
      setSelectedValues(selectedValues.filter((v) => v !== currentItemNum));
    }
  };

  // 
  const onChangeItemCount = (itemnum, count) => {
    // const { name, value } = e.target;
    setItemCount({ ...inputItemCount, [itemnum]: count });
    // setNewOrder(prevCartitem => ({ ...prevCartitem, [name]: value }));
  };

  const handleTermsCheckboxChange = (event) => {
    const { id, checked } = event.target;
    setAgreedToTerms((prevState) => ({
      ...prevState,
      [id]: checked,
    }));
  };

  return (
    <div>
      <div className="logo__container">
        <Header />
      </div>
      <div style={{ maxWidth: 1400, margin: "0 auto" }}>
        <div className="list">
          <div className="list-item" style={{ flex: "auto" }}>
            <h1
              style={{
                marginBottom: 16,
                fontWeight: "bolder",
              }}
            >
               
            </h1>
            <img
              style={{
                position: "absolute",
                width: 190,
                height: 200,
                marginLeft: 31,
              }}
              id="fresh1"
              src={fresh1}
              alt="fresh1"
            />
            <img
              style={{
                position: "absolute",
                width: 190,
                height: 200,
                marginLeft: 273,
              }}
              id="fresh2"
              src={fresh2}
              alt="fresh2"
            />
            <img
              style={{
                position: "absolute",
                width: 190,
                height: 200,
                marginLeft: 515,
              }}
              id="fresh3"
              src={fresh3}
              alt="fresh3"
            />
            <img
              style={{
                position: "absolute",
                width: 190,
                height: 200,
                marginLeft: 756,
              }}
              id="fresh4"
              src={fresh4}
              alt="fresh4"
            />
            <img
              style={{
                position: "absolute",
                width: 190,
                height: 200,
                marginLeft: 31,
                marginTop: 290,
              }}
              id="fresh5"
              src={fresh5}
              alt="fresh5"
            />
            <img
              style={{
                position: "absolute",
                width: 190,
                height: 200,
                marginLeft: 273,
                marginTop: 290,
              }}
              id="fresh6"
              src={fresh6}
              alt="fresh6"
            />
            <img
              style={{
                position: "absolute",
                width: 190,
                height: 200,
                marginLeft: 515,
                marginTop: 290,
              }}
              id="fresh7"
              src={fresh7}
              alt="fresh7"
            />
            <img
              style={{
                position: "absolute",
                width: 190,
                height: 200,
                marginLeft: 756,
                marginTop: 290,
              }}
              id="fresh8"
              src={fresh8}
              alt="fresh8"
            />
            <ul className="item__container">
              {freshitems.map((freshitem) => (
                <li key={freshitem.itemnum} className="item">
                  {/*  */}
                  <div
                    style={{
                      width: 190,
                      height: 200,
                      backgroundColor: "#e0e0e0",
                    }}
                  />
                  {/*  */}
                  <div className="description">
                    {/*  */}
                    <div className="infomation">
                      <p
                        style={{
                          marginTop: 4,
                          marginBottom: 4,
                        }}
                      >
                        <input
                          id={freshitem.itemnum}
                          type="checkbox"
                          value={freshitem.itemnum}
                          style={{ marginRight: 2 }}
                          onChange={handleCheckboxChange}
                        />
                        <label
                          htmlFor={freshitem.itemnum}
                          style={{ fontSize: 15 }}
                        >
                          {freshitem.itemName || " "}
                        </label>
                      </p>
                      <div>
                        <span
                          style={{
                            marginLeft: 14,
                            fontSize: "0.9em",
                            fontWeight: "bolder",
                          }}
                        >
                          {Number(freshitem.itemPrice).toLocaleString()}
                        </span>
                        
                      </div>
                    </div>
                    {/*  */}
                    <div className="amount">
                      <input
                        style={{
                          maxWidth: 55,
                          padding: 2,
                          marginRight: 5,
                          marginLeft: 10,
                        }}
                        type="number"
                        min={0}
                        name="itemCount"
                        placeholder=""
                        value={inputItemCount?.[freshitem.itemnum] || ""}
                        onChange={(e) =>
                          onChangeItemCount(freshitem.itemnum, e.target.value)
                        }
                      />
                      <p style={{ marginTop: 8 }}></p>
                      <div className="res_num">
                        <input
                          className="form-control"
                          type="hidden"
                          name="resnum"
                          value={resnum?.[freshitem.itemnum] || ""}
                        />
                      </div>
                    </div>
                  </div>
                </li>
              ))}
            </ul>
          </div>
          <div className="list-item" style={{ width: "30%" }}>
            <div className="result" style={{ paddingLeft: 16 }}>
              <div></div>
              <div>
                <h1
                  style={{
                    marginBottom: 16,
                    fontWeight: "bolder",
                    textAlign: "right",
                  }}
                >
                  <strong> </strong>
                </h1>
                {orderList?.length < 1 && (
                  <p style={{ textAlign: "center" }}>
                      .
                  </p>
                )}
                <ul className="select_fresh">
                  {orderList.map((targetItem) => {
                    return (
                      <li style={{ paddingTop: 4, textAlign: "right" }}>
                        <span style={{ marginRight: 8 }}>
                          {targetItem.itemName}
                        </span>
                        <span style={{ marginRight: 8 }}>
                          {Number(targetItem.itemCount).toLocaleString()}
                        </span>
                        <span>
                          {Number(targetItem.item_Total_Price).toLocaleString()}
                          
                        </span>
                      </li>
                    );
                  })}
                  <li>
                    <hr />
                  </li>
                  {orderList.length > 0 && (
                    <li style={{ paddingTop: 4, textAlign: "right" }}>
                      <span>
                         :{" "}
                        <strong style={{ fontWeight: "bolder" }}>
                          {Number(totalAmount).toLocaleString()}
                        </strong>
                        
                      </span>
                    </li>
                  )}
                </ul>
              </div>
              <div style={{ textAlign: "right", marginTop: "10px" }}>
                <label htmlFor="terms1">
                  <input
                    type="checkbox"
                    id="terms1"
                    checked={agreedToTerms.terms1}
                    onChange={handleTermsCheckboxChange}
                  />
                     .
                </label>
                <br />
                <label htmlFor="terms2">
                  <input
                    type="checkbox"
                    id="terms2"
                    checked={agreedToTerms.terms2}
                    onChange={handleTermsCheckboxChange}
                  />
                       .
                </label>
              </div>
              <div className="button_align" style={{ textAlign: "right" }}>
                <button
                  className="add__button"
                  onClick={registerAddOrder}
                  style={{ marginTop: 16, padding: 8, width: 365 }}
                  disabled={orderList.length === 0} // orderList    .
                >
                  
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
