import "../css/Header.css";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap";
import React, { useState, useEffect } from "react";
import axios from "axios";
import PenPickLogo from "../img/.png";
import UserImg from "../img/.png";
import user from "../img/.jpg";
import marker from "../img/2.png";

function Header() {
  const [userEmail, setUserEmail] = useState("");
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);

  useEffect(() => {
    //      
    const handleResize = () => {
      setWindowWidth(window.innerWidth);
    };

    window.addEventListener("resize", handleResize);

    //      
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  //    
  const smallScreenFunction = () => {
    return (
      <div id="MainsmallMenu" className="side-menu">
        <div id="sidebarGroup">
          <img src={user} id="MainSideUserImg" alt=""></img>
          <div id="sidebarSign" className="row">
            <a
              id="HeaderDropDownLink2"
              class="dropdown-item col-md-5"
              href="/login"
            >
              
            </a>
            <span id="sidemenuSignUpCenterBar" className="col-md-2">
              |
            </span>
            <a
              id="HeaderDropDownLink2"
              class="dropdown-item col-md-5"
              href="/signUp"
            >
              
            </a>
          </div>
        </div>
        <div id="sidemenuPointBox">
          <button id="sidemenuPoint"></button>|
          <button id="sidemenuPoint"></button>
        </div>
        <a
          class="nav-link active"
          aria-current="page"
          id="HeaderQnALink2"
          href="/QnA"
        >
          <span id="sidemenuArrow"></span>
        </a>
        <a
          class="nav-link active"
          aria-current="page"
          id="HeaderEventLink2"
          href="/EventPage"
        >
          <span id="sidemenuArrow"></span>
        </a>
        <li class="nav-item">
          <a
            id="HeaderCartImg2"
            class="nav-link active"
            aria-current="page"
            href="/FreshHome"
          >
            <span id="sidemenuArrow"></span>
          </a>
        </li>
      </div>
    );
  };

  //    
  const largeScreenFunction = () => {
    return (
      <div>
        {/*     */}
        <ul id="headerbox" class="navbar-nav me-auto mb-2 mb-lg-0">
          <a
            class="nav-link active"
            aria-current="page"
            id="HeaderQnALink"
            href="/QnA"
          >
            
          </a>
          <a
            class="nav-link active"
            aria-current="page"
            id="HeaderEventLink"
            href="/EventPage"
          >
            
          </a>
          <li class="nav-item">
            <a
              id="HeaderCartImg"
              class="nav-link active"
              aria-current="page"
              href="/PrevFresh"
            >
              
            </a>
          </li>

          <li class="nav-item dropdown">
            <a
              class="nav-link"
              href="/"
              role="button"
              data-bs-toggle="dropdown"
              aria-expanded="false"
            >
              <img id="HeaderUserImg" src={UserImg} alt="" />
            </a>
            <ul class="dropdown-menu" id="HeaderDropdownBox">
              {isAuthenticated ? (
                //    
                <>
                  <li>
                    <a
                      class="dropdown-item"
                      id="HeaderDropDownLinkMYPAGE"
                      href="/mypage"
                    >
                      
                    </a>
                  </li>
                  <li>
                    <button
                      class="dropdown-item"
                      id="HeaderDropDownLinkMYPAGE"
                      type="button"
                      onClick={handleLogout}
                    >
                      
                    </button>
                  </li>
                </>
              ) : (
                //    
                <>
                  <li>
                    <a
                      id="HeaderDropDownLink"
                      class="dropdown-item"
                      href="/login"
                    >
                      
                    </a>
                  </li>
                  <li>
                    <a
                      id="HeaderDropDownLink"
                      class="dropdown-item"
                      href="/signUp"
                    >
                      
                    </a>
                  </li>
                </>
              )}
            </ul>
          </li>
        </ul>
      </div>
    );
  };

  //     
  const handleClick = () => {
    //       
    if (windowWidth <= 1000) {
      smallScreenFunction();
    } else {
      largeScreenFunction();
    }
  };

  //  
  useEffect(() => {
    const fetchUserData = async () => {
      try {
        const res = await axios.get("http://localhost:8282/userdata", {
          withCredentials: true,
        });
        setUserEmail(res.data.userEmail);
        setAuthentication(res.data.userEmail);
      } catch (err) {
        console.error("   ", err);
      } finally {
        setIsLoading(false);
      }
    };
    fetchUserData();
  }, []);

  const setAuthentication = (userEmail) => {
    if (userEmail !== "") {
      setIsAuthenticated(true);
    }
  };

  //     X
  if (isLoading) {
    return null;
  }

  const handleLogout = async () => {
    try {
      const response = await axios.post("http://localhost:8282/logout", null, {
        withCredentials: true,
      });
      console.log(response.data);
    } catch (error) {
      console.error(error);
      alert(" ");
      window.location.href = "http://localhost:3000/";
    }
  };

  return (
    <div>
      <div id="HeaderBannerImg">
        <div id="mainPageCenterBox">
          <nav id="HeaderNav" class="navbar navbar-expand-lg ">
            <div class="container-fluid">
              <button
                id="smallMenuBar"
                class="navbar-toggler"
                type="button"
                data-bs-toggle="collapse"
                data-bs-target="#navbarSupportedContent"
                aria-controls="navbarSupportedContent"
                aria-expanded="false"
                aria-label="Toggle navigation"
                onClick={handleClick}
              >
                <span class="navbar-toggler-icon"></span>
              </button>
              <div class="collapse navbar-collapse" id="navbarSupportedContent">
                <ul class="navbar-nav me-auto mb-2 mb-lg-0">
                  {/*   smallScreenFunction ,   largeScreenFunction  */}
                  {windowWidth <= 1000
                    ? smallScreenFunction()
                    : largeScreenFunction()}
                </ul>
              </div>
            </div>
          </nav>
          {/*   */}
          <div id="HeaderBannerLink">
            <a href="/" id="HeaderMainLink">
              <img id="HeaderPenPickImg" src={PenPickLogo} alt=""></img>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Header;
