import React, { useState, useEffect } from "react";
import healingPension from "../img/THE _2_3.jpg";
import mapOption from "../img/  .png";
import "../css/DetailsPage.css";
import { Modal, Button } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import axios from "axios";
import DetailServiceModal from "./DetailPageModal/DetailServiceModal";
import DetailTripleRoomModal from "./DetailPageModal/DetailTripleRoomModal";
import DetailFamilyRoomModal from "./DetailPageModal/DetailFamilyRoomModal";
import DetailGroupRoomModal from "./DetailPageModal/DetailGroupRoomModal";
import DetailDoubleRoomModal from "./DetailPageModal/DetailDoubleRoomModal";
import { useLocation, useNavigate } from "react-router-dom";
import Header from "./Header";
import NoImg from "../img/ .png";
import ReviewList from "./ReviewList";

function DetailsPage() {
  //  
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  // const [isLoading, setIsLoading] = useState(true);

  //   
  const [images, setImages] = useState([]);

  //   
  const [mapModalBtn, setmapModalBtn] = useState(false);

  const handleMapPensionShow = () => setmapModalBtn(true);

  const handleMapPensionClose = () => setmapModalBtn(false);

  //   
  const [ModalButton, setModalButton] = useState(false);

  const handleShow = () => setModalButton(true);

  const handleClose = () => setModalButton(false);

  //  
  const [doubleRoom] = useState(" ");

  const [tripleRoom] = useState(" ");

  const [famillyRoom] = useState(" ");

  const [groupRoom] = useState(" ");

  //  
  const [doubleRoomPrice] = useState(80000);

  const [tripleRoomPrice] = useState(120000);

  const [famillyRoomPrice] = useState(160000);

  const [groupRoomPrice] = useState(240000);

  //  
  const [resCheck, setResCheck] = useState([]);

  //    

  const [resCheckInDate, setResCheckInDate] = useState([]);
  const [resCheckOutDate, setResCheckOutDate] = useState([]);

  //   
  const [reservation, setReservation] = useState(false);

  // pension   Hook
  const [detailPension, setDetailPension] = useState([]);

  // const location = useLocation();

  const [searchDetail, setSearchDetail] = useState("");
  // URLSearchParams :      [  ] (    ex.chrome)

  const location = useLocation();
  const navigate = useNavigate();

  ///////////////////////////////////////////////////////////////////////////////////////////////////////////////////

  // 
  const now = new Date();
  const year = now.getFullYear();
  const month = ("0" + (now.getMonth() + 1)).slice(-2);
  const day = ("0" + now.getDate()).slice(-2);

  //   
  const nextDay = new Date(now);
  nextDay.setDate(nextDay.getDate() + 1);
  const nextDayOfMonth = ("0" + nextDay.getDate()).slice(-2);
  const Month = ("0" + (nextDay.getMonth() + 1)).slice(-2);
  const nextYear = nextDay.getFullYear();

  //     
  const oneMonthLater = new Date(now);
  oneMonthLater.setMonth(oneMonthLater.getMonth() + 1);
  const oneMonthLaterYear = oneMonthLater.getFullYear();
  const oneMonthLaterMonth = ("0" + (oneMonthLater.getMonth() + 1)).slice(-2);
  const oneMonthLaterDay = ("0" + oneMonthLater.getDate()).slice(-2);

  //  
  const [inputcheckinDate, setInputcheckinDate] = useState(
    location.state?.inputcheckinDate
  );

  //  
  const [inputcheckoutDate, setInputcheckoutDate] = useState(
    location.state?.inputcheckoutDate
  );

  // 
  const [peopleNumber, setPeopleNumber] = useState(
    location.state?.peopleNumber
  );

  //  id
  const selectedId = location.state?.selectedId;

  const pensionName = location.state?.pensionName;

  console.log(
    inputcheckinDate,
    inputcheckoutDate,
    peopleNumber,
    selectedId,
    pensionName
  );

  ///////////////////////////////////////////////////////////////////////////////////////////////////////////////////

  const handleReservation = async () => {
    try {
      const res = await axios.get(
        "http://localhost:8282/reservation/getReservation",
        {
          withCredentials: true,
          params: {
            pensions: selectedId,
            checkInDay: inputcheckinDate,
            checkOutDay: inputcheckoutDate,
          },
        }
      );

      // const resData = Array.isArray(res.data) ? res.data : [res.data];
      // console.log(resData);
      const forData = () => {
        for (let i = 0; i <= Object.entries(res.data).length; i++) {
          if (
            res.data[i] !== null &&
            res.data[i] !== resCheck &&
            res.data[i] !== undefined
          ) {
            console.log("false 2");
            return setReservation(false);
          } else {
            console.log("true ");
          }
          return setReservation(true);
        }
      };
      forData();
      console.log("for ");
      console.log(Object.entries(res.data).length);
      console.log(res.data);
    } catch (err) {
      console.log("" + err);
    }
  };

  useEffect(() => {
    fetchUserData();
    handleReview();

    setSearchDetail(selectedId);
    if (selectedId !== null) {
      handleDetail();

      console.log(selectedId);
    } else {
      console.log(" ");
    }
    console.log("22" + reservation.pay);
  }, [searchDetail]);

  const fetchUserData = async () => {
    try {
      const res = await axios.get("http://localhost:8282/userdata", {
        withCredentials: true,
      });
      setAuthentication(res.data.userEmail);
      console.log(res.data.userEmail);
    } catch (err) {
      console.error("   ", err);
    } finally {
      // setIsLoading(false);
      console.log("finally");
    }
  };

  //   
  const setAuthentication = (userEmail) => {
    if (userEmail !== "") {
      setIsAuthenticated(true);
    } else {
      setIsAuthenticated(false);
    }
  };

  const handleReservationPage = (id, room, price) => {
    //  id
    const selectedId = id;

    //  
    const roomType = room;

    //  
    const roomPrice = price;

    console.log(id, room, price);
    navigate("/Reservation", {
      state: {
        inputcheckinDate,
        inputcheckoutDate,
        peopleNumber,
        selectedId,
        roomType,
        roomPrice,
      },
    });
  };

  const handleFalseAlert = (isAuthenticated, reservation) => {
    if (isAuthenticated !== true) {
      alert(" .");
      window.location.href = "/login";
    } else if (
      inputcheckinDate === undefined ||
      inputcheckoutDate === undefined ||
      peopleNumber === undefined
    ) {
      alert("    .");
    } else if (reservation !== true) {
      alert("    .");
    } else {
      alert("   .");
    }
  };

  const handleDetail = async () => {
    try {
      const res = await axios.get(`http://localhost:8282/penpick/details`, {
        withCredentials: true,
        params: {
          id: searchDetail,
        },
      });
      console.log(res.data);
      setDetailPension(res.data);
      loadKakaoMap(res.data);
      console.log(detailPension);
    } catch (error) {
      console.error("Error", error);
    }
  };
  useEffect(() => {
    loadKakaoMap(detailPension);
    handleReservation();
    if (detailPension !== null) {
      fetchImages();
    }
  }, [resCheckOutDate]);

  const fetchImages = async () => {
    try {
      const response = await fetch(
        `http://localhost:8282/details?name=${encodeURIComponent(pensionName)}`,
        {
          withCredentials: true,
        }
      );
      if (!response.ok) {
        throw new Error("  .");
      }

      const imagesData = await response.json();
      setImages(imagesData);
      console.log("");
      console.log(pensionName);
      console.log(imagesData);
    } catch (error) {
      console.error(" .:", error);
    }
  };

  // 
  const [review, setReview] = useState([]);
  const handleReview = async () => {
    try {
      const res = await axios.get(`http://localhost:8282/review/detailReview`, {
        withCredentials: true,
        params: {
          id: selectedId,
        },
      });
      console.log(res + "");
      console.log(res.data);
      setReview(res.data);
    } catch (err) {
      console.log(err);
    }
  };

  const loadKakaoMap = (pension) => {
    const script = document.createElement("script");
    script.async = true;
    script.src =
      "https://dapi.kakao.com/v2/maps/sdk.js?appkey=bc0b0a476c690ff9dfdbc6a531ebf7b4&autoload=false";
    document.head.appendChild(script);

    script.onload = () => {
      window.kakao.maps.load(() => {
        //   window.kakao.maps   .
        const container = document.getElementById("map");
        // console.log("1");
        const container2 = document.getElementById("map2");
        // console.log("2");

        const options = {
          center: new window.kakao.maps.LatLng(
            pension.latitude,
            pension.longitude
          ),
          level: 3,
        };
        // console.log("4");
        const options2 = {
          center: new window.kakao.maps.LatLng(
            pension.latitude,
            pension.longitude
          ),
          level: 3,
        };

        const map = new window.kakao.maps.Map(container, options);
        // console.log("5");
        const map2 = new window.kakao.maps.Map(container2, options2);
        // console.log("6");

        var mapTypeControl = new window.kakao.maps.MapTypeControl();
        // console.log("8");

        map.addControl(
          mapTypeControl,
          window.kakao.maps.ControlPosition.TOPRIGHT
        );
        // console.log("9");
        map2.addControl(
          mapTypeControl,
          window.kakao.maps.ControlPosition.TOPRIGHT
        );
        // console.log("10");

        const marker = new window.kakao.maps.Marker({
          map: map,
          position: new window.kakao.maps.LatLng(
            pension.latitude,
            pension.longitude
          ),
          title: pension.name,
        });
        // console.log("12");
        const marker2 = new window.kakao.maps.Marker({
          map: map2,
          position: new window.kakao.maps.LatLng(
            pension.latitude,
            pension.longitude
          ),
          title: pension.name,
        });
        // console.log("13");
        var content = `<div class="wrap">
              <div class="info">
                  <div class="title" > 
          ${detailPension.name}
                      <div class="close" onclick="closeOverlay()" title=""></div>
                  </div>
                 <div class="body">
                      <div class="img">
                          <img src=${healingPension} width="73" height="70">
                     </div>
                      <div class="desc">
                          <div class="ellipsis">${detailPension.address}</div>
                      </div>
                  </div>
              </div>
          </div>`;

        //    
        //      CSS   

        var overlay2 = new window.kakao.maps.CustomOverlay({
          content: content,
          map: map2,
          position: marker.getPosition(),
        });

        let markerOpen = false;
        let markerOpen2 = false;
        //      

        window.kakao.maps.event.addListener(marker2, "click", function () {
          if (markerOpen2) {
            overlay2.setMap(null);
            markerOpen2 = false;
          } else {
            overlay2.setMap(map2);
            markerOpen2 = true;
          }
        });

        if (mapModalBtn === true) {
          const container3 = document.getElementById("map3");
          console.log("container3");
          const options3 = {
            center: new window.kakao.maps.LatLng(
              pension.latitude,
              pension.longitude
            ),
            level: 3,
          };
          console.log("options3");
          const map3 = new window.kakao.maps.Map(container3, options3);
          console.log("map3");
          var mapTypeControl = new window.kakao.maps.MapTypeControl();
          map3.addControl(
            mapTypeControl,
            window.kakao.maps.ControlPosition.TOPRIGHT
          );
          console.log("map3.addControl");
          const marker3 = new window.kakao.maps.Marker({
            map: map3,
            position: new window.kakao.maps.LatLng(
              pension.latitude,
              pension.longitude
            ),
            title: pension.name,
          });
          console.log("marker3");
          var overlay3 = new window.kakao.maps.CustomOverlay({
            content: content,
            map: map3,
            position: marker.getPosition(),
          });

          let markerOpen3 = false;
          window.kakao.maps.event.addListener(marker3, "click", function () {
            if (markerOpen3) {
              overlay3.setMap(null);
              markerOpen3 = false;
            } else {
              overlay3.setMap(map3);
              markerOpen3 = true;
            }
          });
        }
      });
    };
  };

  return (
    <div>
      <Header />

      <div id="detailpage">
        <div>
          <div id="detailpage-sub">
            <section id="detail-img-section">
              <div>
                <a id="detail-img-container">
                  <div id="detail-main-img-grid">
                    {images[0] ? (
                      <div id="detail-main-img1">
                        {images.length > 0 && images[0].imageData && (
                          <img
                            id="detail-main-img1"
                            src={`data:image/jpeg;base64,${images[0].imageData}`}
                            alt={images[0].imageName}
                            onClick={handleShow}
                          />
                        )}
                      </div>
                    ) : (
                      <img
                        id="detail-main-img1"
                        src={NoImg}
                        alt=" ."
                        onClick={handleShow}
                      />
                    )}
                  </div>
                  <div id="detail-sub-img-grid">
                    {images[1] ? (
                      <div id="detail-sub-img2">
                        {images.length > 0 && images[1].imageData && (
                          <img
                            id="detail-sub-img2"
                            src={`data:image/jpeg;base64,${images[1].imageData}`}
                            alt={images[1].imageName}
                            onClick={handleShow}
                          />
                        )}
                      </div>
                    ) : (
                      <img
                        id="detail-sub-img2"
                        src={NoImg}
                        alt=" ."
                        onClick={handleShow}
                      />
                    )}
                  </div>
                  <div id="detail-sub-img-grid">
                    {images[2] ? (
                      <div id="detail-sub-img3">
                        {images.length > 0 && images[2].imageData && (
                          <img
                            id="detail-sub-img3"
                            src={`data:image/jpeg;base64,${images[2].imageData}`}
                            alt={images[2].imageName}
                            onClick={handleShow}
                          />
                        )}
                      </div>
                    ) : (
                      <img
                        id="detail-sub-img3"
                        src={NoImg}
                        alt=" ."
                        onClick={handleShow}
                      />
                    )}
                  </div>
                  <div id="detail-sub-img-grid">
                    {images[3] ? (
                      <div id="detail-sub-img4">
                        {images.length > 0 && images[3].imageData && (
                          <img
                            id="detail-sub-img4"
                            src={`data:image/jpeg;base64,${images[3].imageData}`}
                            alt={images[3].imageName}
                            onClick={handleShow}
                          />
                        )}
                      </div>
                    ) : (
                      <img
                        id="detail-sub-img4"
                        src={NoImg}
                        alt=" ."
                        onClick={handleShow}
                      />
                    )}
                  </div>
                  <div id="detail-sub-img-grid">
                    {images[4] ? (
                      <div id="detail-sub-img5">
                        {images.length > 0 && images[4].imageData && (
                          <img
                            id="detail-sub-img5"
                            src={`data:image/jpeg;base64,${images[4].imageData}`}
                            alt={images[4].imageName}
                            onClick={handleShow}
                          />
                        )}
                      </div>
                    ) : (
                      <img
                        id="detail-sub-img5"
                        src={NoImg}
                        alt=" ."
                        onClick={handleShow}
                      />
                    )}
                    <div id="modal-btn">
                      <Button
                        className="btn"
                        style={{
                          background: "white",
                          border: "none",
                          borderRadius: "30px",
                          color: "black",
                          boxShadow: "0px 0px 5px black",
                          fontSize: "12px",
                          fontWeight: "600",
                          padding: "12px 20px 12px 20px",
                        }}
                        onClick={handleShow}
                      >
                         
                      </Button>
                      <Modal
                        id="modalpage"
                        show={ModalButton}
                        onHide={handleClose}
                      >
                        <Modal.Header id="modal-header-title" closeButton>
                          <Modal.Title id="modal-header-title">
                            {detailPension.name}
                          </Modal.Title>
                        </Modal.Header>
                        <Modal.Body id="modal-body-img">
                          <div id="modal-body-div">
                            {images.length > 0 && images[0].imageData && (
                              <img
                                id="modal-main-img"
                                src={`data:image/jpeg;base64,${images[0].imageData}`}
                                alt={images[0].imageName}
                              />
                            )}
                          </div>
                        </Modal.Body>
                        <Modal.Footer>
                          {images.map(
                            (image, index) =>
                              image &&
                              image.imageData && (
                                <img
                                  key={index}
                                  id="detail-img-map"
                                  src={`data:image/jpeg;base64,${image.imageData}`}
                                  alt={image.imageName}
                                />
                              )
                          )}
                        </Modal.Footer>
                      </Modal>
                    </div>
                  </div>
                </a>
              </div>
            </section>
          </div>
          <section id="pension-service-section">
            <div id="pension-div-box">
              <div id="detail-title">
                <p id="detail-title-text"></p>
                <h3 id="detail-title-name">{detailPension.name}</h3>
              </div>
              <div id="detail-PensionSearchForm">
                <input
                  id="detail-HeaderPensionInputDate1"
                  value={inputcheckinDate}
                  min={inputcheckinDate}
                  max={oneMonthLaterYear + "-" + oneMonthLaterMonth + "-" + day}
                  type="date"
                  onChange={(e) => setInputcheckinDate(e.target.value)}
                />
                <span id="detail-InputBar">|</span>
                <input
                  id="detail-HeaderPensionInputDate2"
                  min={inputcheckinDate}
                  value={inputcheckoutDate}
                  max={
                    oneMonthLaterYear +
                    "-" +
                    oneMonthLaterMonth +
                    "-" +
                    nextDayOfMonth
                  }
                  type="date"
                  onChange={(e) => setInputcheckoutDate(e.target.value)}
                />
                <span id="detail-InputBar"></span>
                <input
                  id="detail-HeaderPensionInputNumber"
                  value={peopleNumber}
                  type="number"
                  onChange={(e) => setPeopleNumber(e.target.value)}
                  min={1}
                  max={8}
                  onClick={handleReservation}
                />
                
              </div>

              <div id="detail-service-container">
                <div id="detail-lines" />
                <div id="detail-lines-title">
                    
                  <span>
                    <DetailServiceModal />
                  </span>
                </div>
                <div id="detail-linse-text">
                  Parking : {detailPension.parking}
                  <br />
                  Cook : {detailPension.cook}
                  <br />
                  Dining Hall : {detailPension.dininghall}
                  <br />
                  Amenities : {detailPension.amenities}
                </div>
                <div id="detail-lines" />
                <div id="detail-lines-title"> </div>
                <div id="detail-room-container">
                  <div id="detail-room-sub-container">
                    <div id="detail-room-imgbox">
                      {images[0] ? (
                        <div id="detail-room-img">
                          {images.length > 0 && images[0].imageData && (
                            <img
                              id="detail-room-img"
                              src={`data:image/jpeg;base64,${images[0].imageData}`}
                              alt={images[0].imageName}
                              onClick={handleShow}
                            />
                          )}
                        </div>
                      ) : (
                        <img
                          id="detail-room-img"
                          src={healingPension}
                          alt=" ."
                          onClick={handleShow}
                        />
                      )}

                      <h5 id="detail-room-name"> </h5>
                      <div>
                        <DetailDoubleRoomModal />
                        <br />
                        <div id="detail-room-checkIObox">
                          <div>
                            <div id="room-check-in">
                               {detailPension.check_in}
                              <br />
                               {detailPension.check_out}
                            </div>
                            <div id="room-reservation-text">
                              <div id="room-reservation-price">80,000</div>

                              <div>
                                {peopleNumber >= 2 &&
                                peopleNumber <= 3 &&
                                isAuthenticated !== false &&
                                reservation !== false ? (
                                  <Button
                                    class="btn btn-primary"
                                    id="reservation-btn"
                                    onClick={() =>
                                      handleReservationPage(
                                        searchDetail,
                                        doubleRoom,
                                        doubleRoomPrice
                                      )
                                    }
                                  >
                                     
                                  </Button>
                                ) : (
                                  <Button
                                    class="btn btn-danger"
                                    id="reservation-btn-false"
                                    onClick={() =>
                                      handleFalseAlert(
                                        isAuthenticated,
                                        reservation
                                      )
                                    }
                                  >
                                     
                                  </Button>
                                )}
                              </div>
                            </div>
                          </div>
                        </div>
                        <div id="detail-room-people">
                          <span id="detail-room-people-title">
                            {" "}
                            <span id="detail-room-people-text">
                              2  3 (){" "}
                            </span>
                          </span>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div id="detail-room-container">
                  <div id="detail-room-sub-container">
                    <div id="detail-room-imgbox">
                      {images[1] ? (
                        <div id="detail-room-img">
                          {images.length > 0 && images[1].imageData && (
                            <img
                              id="detail-room-img"
                              src={`data:image/jpeg;base64,${images[1].imageData}`}
                              alt={images[1].imageName}
                              onClick={handleShow}
                            />
                          )}
                        </div>
                      ) : (
                        <img
                          id="detail-room-img"
                          src={healingPension}
                          alt=" ."
                          onClick={handleShow}
                        />
                      )}
                      <h5 id="detail-room-name"> </h5>
                      <div>
                        <DetailTripleRoomModal />
                        <br />
                        <div id="detail-room-checkIObox">
                          <div>
                            <div id="room-check-in">
                               {detailPension.check_in}
                              <br />
                               {detailPension.check_out}
                            </div>
                            <div id="room-reservation-text">
                              <div id="room-reservation-price">120,000</div>

                              <div>
                                {peopleNumber >= 3 &&
                                peopleNumber <= 4 &&
                                isAuthenticated !== false &&
                                reservation !== false ? (
                                  <Button
                                    class="btn btn-primary"
                                    id="reservation-btn"
                                    onClick={() =>
                                      handleReservationPage(
                                        searchDetail,
                                        tripleRoom,
                                        tripleRoomPrice
                                      )
                                    }
                                  >
                                     
                                  </Button>
                                ) : (
                                  <Button
                                    class="btn btn-danger"
                                    id="reservation-btn-false"
                                    onClick={() =>
                                      handleFalseAlert(
                                        isAuthenticated,
                                        reservation
                                      )
                                    }
                                  >
                                     
                                  </Button>
                                )}
                              </div>
                            </div>
                          </div>
                        </div>
                        <div id="detail-room-people">
                          <span id="detail-room-people-title">
                            {" "}
                            <span id="detail-room-people-text">
                              3  4 (){" "}
                            </span>
                          </span>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div id="detail-room-container">
                  <div id="detail-room-sub-container">
                    <div id="detail-room-imgbox">
                      {images[2] ? (
                        <div id="detail-room-img">
                          {images.length > 0 && images[2].imageData && (
                            <img
                              id="detail-room-img"
                              src={`data:image/jpeg;base64,${images[2].imageData}`}
                              alt={images[2].imageName}
                              onClick={handleShow}
                            />
                          )}
                        </div>
                      ) : (
                        <img
                          id="detail-room-img"
                          src={healingPension}
                          alt=" ."
                          onClick={handleShow}
                        />
                      )}
                      <h5 id="detail-room-name"> </h5>
                      <div>
                        <DetailFamilyRoomModal />
                        <br />
                        <div id="detail-room-checkIObox">
                          <div>
                            <div id="room-check-in">
                               {detailPension.check_in}
                              <br />
                               {detailPension.check_out}
                            </div>
                            <div id="room-reservation-text">
                              <div id="room-reservation-price">160,000</div>

                              <div>
                                {peopleNumber >= 4 &&
                                peopleNumber <= 5 &&
                                isAuthenticated !== false &&
                                reservation !== false ? (
                                  <Button
                                    class="btn btn-primary"
                                    id="reservation-btn"
                                    onClick={() =>
                                      handleReservationPage(
                                        searchDetail,
                                        famillyRoom,
                                        famillyRoomPrice
                                      )
                                    }
                                  >
                                     
                                  </Button>
                                ) : (
                                  <Button
                                    class="btn btn-danger"
                                    id="reservation-btn-false"
                                    onClick={() =>
                                      handleFalseAlert(
                                        isAuthenticated,
                                        reservation
                                      )
                                    }
                                  >
                                     
                                  </Button>
                                )}
                              </div>
                            </div>
                          </div>
                        </div>
                        <div id="detail-room-people">
                          <span id="detail-room-people-title">
                            {" "}
                            <span id="detail-room-people-text">
                              4  5 (){" "}
                            </span>
                          </span>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div id="detail-room-container">
                  <div id="detail-room-sub-container">
                    <div id="detail-room-imgbox">
                      {images[3] ? (
                        <div id="detail-room-img">
                          {images.length > 0 && images[3].imageData && (
                            <img
                              id="detail-room-img"
                              src={`data:image/jpeg;base64,${images[3].imageData}`}
                              alt={images[3].imageName}
                              onClick={handleShow}
                            />
                          )}
                        </div>
                      ) : (
                        <img
                          id="detail-room-img"
                          src={healingPension}
                          alt=" ."
                          onClick={handleShow}
                        />
                      )}
                      <h5 id="detail-room-name"> </h5>
                      <div>
                        <DetailGroupRoomModal />
                        <br />
                        <div id="detail-room-checkIObox">
                          <div>
                            <div id="room-check-in">
                               {detailPension.check_in}
                              <br />
                               {detailPension.check_out}
                            </div>
                            <div id="room-reservation-text">
                              <div id="room-reservation-price">240,000</div>

                              <div>
                                {peopleNumber >= 5 &&
                                peopleNumber <= 8 &&
                                isAuthenticated !== false &&
                                reservation !== false ? (
                                  <Button
                                    class="btn btn-primary"
                                    id="reservation-btn"
                                    onClick={() =>
                                      handleReservationPage(
                                        searchDetail,
                                        groupRoom,
                                        groupRoomPrice
                                      )
                                    }
                                  >
                                     
                                  </Button>
                                ) : (
                                  <Button
                                    class="btn btn-danger"
                                    id="reservation-btn-false"
                                    onClick={() =>
                                      handleFalseAlert(
                                        isAuthenticated,
                                        reservation
                                      )
                                    }
                                  >
                                     
                                  </Button>
                                )}
                              </div>
                            </div>
                          </div>
                        </div>
                        <div id="detail-room-people">
                          <span id="detail-room-people-title">
                            {" "}
                            <span id="detail-room-people-text">
                              5  8 (){" "}
                            </span>
                          </span>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div id="detail-lines" />
                <div id="detail-lines-title"> </div>
                <div id="detail-lines-text">{detailPension.introduction}</div>
                <div id="detail-lines" />
                <div id="detail-lines-title">  </div>
                <div id="detail-linse-head-text">
                  
                  {/*  NULL     */}
                  <div>
                    <li id="detail-li-text">
                       : {detailPension.check_in} |  :
                      {detailPension.check_out}
                    </li>
                    <li id="detail-li-text">
                        : {detailPension.parking}
                    </li>
                    <li id="detail-li-text">
                      {" "}
                        : {detailPension.cook}
                    </li>
                    {/* COOK  NULL       */}
                    <li id="detail-li-text">
                      Dining Hall : {detailPension.dininghall}
                    </li>
                    <li id="detail-li-text">
                      Amenities : {detailPension.amenities}
                    </li>
                    {/* 
                      DB    
                      DINING HALL    
                      AMENITIES   
                          
                  */}
                  </div>
                </div>
                <div id="detail-linse-head-text">
                   
                  <div>
                    <li id="detail-li-text-caution">{detailPension.scale}</li>
                    <li id="detail-li-text">
                        : , , , 
                    </li>
                  </div>
                </div>
                <div id="detail-linse-head-text">
                    
                  <div>
                    <li id="detail-li-text">
                      1 20,000 (24 ~13 ), 40,000 (13 )
                    </li>
                    <li id="detail-li-text">   1  </li>
                    <li id="detail-li-text">
                              
                    </li>
                    <li id="detail-li-text"> </li>
                    <li id="detail-li-text"> </li>
                    <li id="detail-li-text">
                           
                    </li>
                  </div>
                </div>
                <div id="detail-linse-head-text">
                  Dining Hall
                  <div>
                    <li id="detail-li-text">{detailPension.dininghall}</li>
                  </div>
                </div>
                <div id="detail-linse-head-text">
                   
                  <div>
                    <li id="detail-li-text-caution">
                            |{" "}
                      {detailPension.contact}
                    </li>
                    <li id="detail-li-text-caution">
                      {detailPension.amenities}
                    </li>
                  </div>
                </div>
                <div id="detail-linse-head-text">
                   
                  <div>
                    <li id="detail-li-text-caution">
                         : {detailPension.barbeque}
                    </li>
                    <li id="detail-li-text">
                      +  : 2  20,000 (1  5,000) /
                       : 1SET 20,000
                    </li>
                    <li id="detail-li-text">
                       : + (15:00~23:00),  (15:00~23:00)
                    </li>
                    <li id="detail-li-text">
                       : + ( /   
                      ),  ( )
                    </li>
                    <li id="detail-li-text"> </li>
                  </div>
                </div>
                <div id="detail-lines" />
                <div id="detail-head-refund">
                     
                  <div id="detail-li-caution-head">
                    <li id="detail-li-text-caution">{detailPension.refund}</li>
                    <li id="detail-li-text">
                      ,      
                    </li>
                  </div>
                </div>
                <div id="detail-lines" />
              </div>
              <section id="detail-bottom-section">
                <div id="detail-review-map-secsion">
                  
                  <div id="detail-bottom-kakao-map">
                    <div
                      id="map2"
                      style={{
                        width: 1200,
                        height: 480,
                        marginBottom: 10,
                        zIndex: 0,
                      }}
                    />
                    <div id="detail-bottom-kakao-map-address">
                      {detailPension.address}
                    </div>
                  </div>
                </div>
                <div id="detail-review-bottom-section">
                  <div>
                    <div id="reviewList-main-bigbox">
                      <div id="reviewList-head-div">
                        <span id="reviewList-realreview">  </span>
                        {"  "}
                        <span id="reviewList-review-length">
                          {review.length}   {review.length} 
                        </span>
                      </div>

                      {review.map((reviews) => (
                        <div key={reviews.id} id="reviewList-map-box">
                          <span id="reviewList-nickName-text">
                            {reviews.nickName}
                          </span>

                          <div id="reviewList-box-container">
                            <div>{reviews.reviewText}</div>
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>
                </div>
              </section>
            </div>
            <div id="detail-page-right-map-coupon-box">
              <div id="detail-right-map-box">
                <div id="map-short-box">
                  <div id="map"></div>
                  <div id="map-btn-icon">
                    <Button
                      className="btn"
                      style={{
                        background: "white",
                        border: "1px solid rgba(0, 0, 0, 0.1)",
                        borderRadius: "30px",
                        color: "black",
                        position: "absolute",
                        bottom: "15px",
                        left: "280px",
                        zIndex: "1",
                        width: "40px",
                        height: "40px",
                        padding: "0",
                      }}
                      onClick={handleMapPensionShow}
                    >
                      <img
                        id="mapOption-btn-img"
                        src={mapOption}
                        alt=" "
                      />
                    </Button>
                    <Modal
                      id="modalpage"
                      show={mapModalBtn}
                      onHide={handleMapPensionClose}
                    >
                      <Modal.Header id="modal-header-title" closeButton>
                        <Modal.Title id="modal-header-title">
                          {detailPension.name}
                        </Modal.Title>
                      </Modal.Header>
                      <Modal.Body id="modal-body-img">
                        <div>
                          <div
                            id="map3"
                            style={{
                              width: "100%",
                              height: 800,
                              borderRadius: 15,
                            }}
                          />
                        </div>
                      </Modal.Body>
                    </Modal>
                  </div>
                </div>
                <div id="map-pension-name">{detailPension.address}</div>
              </div>
              <br />
              <div id="detail-right-coupon-box"> </div>
            </div>
          </section>
        </div>
      </div>
    </div>
  );
}

export default DetailsPage;
