import React, { useState, useEffect } from "react";
import axios from "axios";
import "../css/PasswordModal.css";
import "bootstrap/dist/css/bootstrap.min.css";
import $ from "jquery";

const PasswordModal = ({ isOpen, onClose }) => {
  // 
  const [currentPassword, setCurrentPassword] = useState("");

  //  
  const [newPassword, setNewPassword] = useState("");

  //   
  const [confirmNewPassword, setConfirmNewPassword] = useState("");

  // (X)    
  const resetFields = () => {
    setCurrentPassword("");
    setNewPassword("");
    setConfirmNewPassword("");
  };

  //  
  //   
  useEffect(() => {
    //         ( )
    const fetchUserData = async () => {
      try {
        const res = await axios.get("http://localhost:8282/userdata", {
          withCredentials: true,
        });
        console.log(res.data);
      } catch (err) {
        console.error("   ", err);
      }
    };
    fetchUserData();
  }, []);

  // ,         
  useEffect(() => {
    passwordCheck();
  }, [newPassword, confirmNewPassword]);

  //  
  const passwordCheck = () => {
    const newPasswordValue = $("#new-password-input").val();
    const confirmNewPasswordValue = $("#confirm-new-password-input").val();

    if (newPasswordValue === confirmNewPasswordValue) {
      $("#pwConfirm").text(" ").css("color", "green");
    } else {
      $("#pwConfirm").text("  ").css("color", "red");
    }
  };

  //     
  const handleChangePassword = async () => {
    //    
    const newPasswordValue = $("#new-password-input").val();
    const confirmNewPasswordValue = $("#confirm-new-password-input").val();

    if (newPasswordValue !== confirmNewPasswordValue) {
      alert("     .");
      return;
    }

    try {
      const response = await axios.put(
        "http://localhost:8282/api/user/changePassword",
        {
          currentPassword,
          newPassword,
        },
        {
          withCredentials: true,
        }
      );

      alert(" ");
      window.location.href = "http://localhost:3000/";
    } catch (error) {
      console.error("  ", error);
      //   ..
      alert("   .");
    }
  };

  return (
    <div className={`password-modal ${isOpen ? "open" : ""}`}>
      <div className="modal-content" id="pw-modal-content">
        <button
          class="btn-close"
          onClick={() => {
            onClose();
            resetFields();
          }}
        />
        <br />
        <h3 style={{ textAlign: "center", fontWeight: "bold" }}>
           
        </h3>
        <br />

        <label id="current-password-info"> </label>
        <input
          type="password"
          name="currentPassword"
          id="current-password-input"
          value={currentPassword}
          onChange={(e) => setCurrentPassword(e.target.value)}
        />
        <br />

        <label id="new-password-info"> </label>
        <input
          type="password"
          name="newPassword"
          id="new-password-input"
          value={newPassword}
          onInput={passwordCheck}
          onChange={(e) => setNewPassword(e.target.value)}
        />
        <br />

        <label id="confirm-new-password-info">  </label>
        <input
          type="password"
          name="confirmNewPassword"
          id="confirm-new-password-input"
          value={confirmNewPassword}
          onInput={passwordCheck}
          onChange={(e) => setConfirmNewPassword(e.target.value)}
        />
        <span
          id="pwConfirm"
          style={{
            marginTop: "2px",
            color: "gray",
            fontSize: "small",
          }}
        />
        <br />
        <br />

        <button id="changePassword-button" onClick={handleChangePassword}>
           
        </button>
      </div>
    </div>
  );
};

export default PasswordModal;
