import React from "react";
import Header from "./Header";
import lottoimg from "../img/.png";
import coinimg from "../img/coin2.png";
import fortuneImg from "../img/3.png";
import fortuneImg2 from "../img/2.png";

function GameLand() {
  return (
    <div>
      <Header />
      <div id="GamelandContainer">
        <div id="GamelandSecondContainer">
          <a href="/GameLand">
            <span id="gamelandtitle"></span>
          </a>
          <img src={coinimg} alt="" id="coinimg" />
          <div>
            <h6 id="gamedescription">
                  !
            </h6>
            <h6>   ....?</h6>
          </div>
          <div id="GameLandcontainer">
            <span id="windowpopbutton1">_</span>
            <span id="windowpopbutton2"></span>
            <a href="GameLand">
              <span id="windowpopbutton3">X</span>
            </a>
            <span id="lottonumbertitle">  </span>
            <div id="gamelandgrid">
              <div id="gamelandtype1box">
                <a href="Randomnumber" id="RouleletteLink">
                  <img src={lottoimg} id="lottoimg" alt="" />
                  <div id="onegameball"></div>
                  <h6 id="gamelandtype1"> </h6>
                </a>
                <h6 id="gamelanddescription">
                     ?
                  <br />
                    !
                </h6>
              </div>
              <div id="gamelandtype1box">
                <a href="FortuneCookie" id="RouleletteLink">
                  <img src={fortuneImg} id="fortuneImg2" alt="" />
                  <h6 id="gamelandtype1"> </h6>
                </a>
                <h6 id="gamelanddescription">
                     ?
                  <br />
                    !
                </h6>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default GameLand;
