import React, { useState, useEffect } from "react";
import "../css/MyPage.css";
import axios from "axios";
import "bootstrap/dist/css/bootstrap.min.css";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import Header from "./Header";
import PasswordModal from "./PasswordModal";
import houseImage from "../img/User-Profile-PNG-Download-Image.png";

export default function MyPage() {
  const [userInfo, setUserInfo] = useState({});
  const [editedUserInfo, setEditedUserInfo] = useState({});
  const [isEditing, setIsEditing] = useState(false);
  const [loading, setLoading] = useState(true);

  //  
  const [hasAccessToken, setHasAccessToken] = useState(false);

  // 
  const [isModalOpen, setIsModalOpen] = useState(false);

  //   
  useEffect(() => {
    //         ( )
    const fetchUserData = async () => {
      try {
        const res = await axios.get("http://localhost:8282/userdata", {
          withCredentials: true,
        });
        setUserInfo(res.data);
        setEditedUserInfo(res.data);

        console.log(res.data.access_token);

        if (res.data.access_token !== null) {
          setHasAccessToken(true);
        } else {
          setHasAccessToken(false);
        }
      } catch (err) {
        console.error("   ", err);
      } finally {
        setLoading(false);
      }
    };
    fetchUserData();
  }, []);

  //    -> input value 

  const GenderValue = (userInfo) => {
    const gender = userInfo.gender;
    if (gender === "F") {
      return "";
    } else {
      return "";
    }
  };

  const handleEdit = () => {
    setIsEditing(true);
  };

  const handleSave = async () => {
    try {
      const response = await axios.put(
        "http://localhost:8282/api/user/update",
        editedUserInfo,
        {
          withCredentials: true,
        }
      );

      setUserInfo(response.data);
      setIsEditing(false);
      alert("  ");
    } catch (error) {
      console.error("   ", error);
    }
  };

  const handleInputChange = (e) => {
    setEditedUserInfo({ ...editedUserInfo, [e.target.name]: e.target.value });
  };

  //    ------------------------------------

  const handleSignOut = async () => {
    if (window.confirm("PenPick   ? (...)")) {
      try {
        const response = await axios.delete(
          "http://localhost:8282/api/user/signOut",
          {
            withCredentials: true,
          }
        );

        alert("  ");
        window.location.href = "http://localhost:3000/";
      } catch (error) {
        console.error("  ", error);
      }
    } else {
      alert("  ");
      return;
    }
  };

  //     ------------------------

  const toggleModal = () => {
    setIsModalOpen(!isModalOpen);
  };

  if (loading) {
    return null;
  }

  return (
    <div>
      <Header />
      <main id="myPage-layout">
        <div id="myPage-container">
          <nav id="myPage-navigation">
            <ul id="navigation-list">
              <li id="nav-userInfo">
                <a href="/mypage">  </a>
              </li>
              <hr />
              <li>
                <a href="/reservationCheck"></a>
              </li>
              <hr />
              <li>
                <a href="/mypage/userInfo"></a>
              </li>
              <hr />
              <li>
                <a href="/mypage/userInfo"></a>
              </li>
              <hr />
              <li id="nav-coupon">
                <a href="/mypage/userInfo"></a>
              </li>
            </ul>
          </nav>
          {/*     */}
          <section id="myPage-content">
            <div>
              <img
                src={houseImage}
                alt=""
                style={{
                  float: "right",
                  width: "130px",
                  marginTop: "10px",
                  marginRight: "10px",
                }}
              />
              <p id="content-title">  </p>
              <br />
              <span style={{ color: "orangered", fontWeight: "bold" }}>
                {userInfo.nickname}
              </span>
              <span>  </span>
            </div>
            <hr />
            {isEditing ? (
              //      
              <div>
                <label id="user-email-info"></label>
                <br />
                <input
                  name="userEmail"
                  id="user-email-edit"
                  value={editedUserInfo.userEmail}
                  readOnly
                />
                <br />
                <label id="user-nickname-info"></label>
                <br />
                <input
                  type="text"
                  name="nickname"
                  id="user-nickname-edit"
                  value={editedUserInfo.nickname}
                  onChange={handleInputChange}
                />
                <br />
                <label id="user-phone-info"></label>
                <br />
                <input
                  type="text"
                  name="phoneNumber"
                  id="user-phone-edit"
                  value={editedUserInfo.phoneNumber}
                  onChange={handleInputChange}
                />
                <br />
                <br />
                <div>
                  <label
                    style={{
                      float: "left",
                      fontSize: "small",
                      marginLeft: "2px",
                    }}
                  >
                    
                  </label>
                  <br />
                </div>
                <div class="form-check form-check-inline" id="female">
                  <input
                    class="form-check-input"
                    type="radio"
                    name="gender"
                    id="inlineRadio1"
                    value="F"
                    checked={editedUserInfo.gender === "F"}
                    onChange={handleInputChange}
                  />
                  <label class="form-check-label" for="inlineRadio1">
                    
                  </label>
                </div>
                <div class="form-check form-check-inline" id="male">
                  <input
                    class="form-check-input"
                    type="radio"
                    name="gender"
                    id="inlineRadio2"
                    value="M"
                    checked={editedUserInfo.gender === "M"}
                    onChange={handleInputChange}
                  />
                  <label class="form-check-label" for="inlineRadio2">
                    
                  </label>
                </div>
                <br />
                <label id="user-phone-info"></label>
                <br />
                <DatePicker
                  id="user-birth-edit"
                  selected={
                    editedUserInfo.birthday
                      ? new Date(editedUserInfo.birthday)
                      : null
                  }
                  onChange={(date) =>
                    setEditedUserInfo((prevUser) => ({
                      ...prevUser,
                      birthday: date,
                    }))
                  }
                  dateFormat="yyyy-MM-dd"
                  showYearDropdown
                  placeholderText="   "
                />
                <br />
                <button id="saveEditInfo" onClick={handleSave}>
                    
                </button>
              </div>
            ) : (
              //    (DB  )
              <div>
                <label id="user-email-info"></label>
                <br />
                <input
                  name="userEmail"
                  id="user-email-value"
                  value={userInfo.userEmail}
                  readOnly
                />
                <br />
                <label id="user-nickname-info"></label>
                <br />
                <input
                  id="user-nickname-value"
                  value={userInfo.nickname}
                  readOnly
                />
                <br />
                <label id="user-phone-info"> </label>
                <br />
                <input
                  id="user-phone-value"
                  value={userInfo.phoneNumber}
                  readOnly
                />
                <br />
                <label id="user-gender-info"></label>
                <br />
                <input
                  id="user-gender-value"
                  value={GenderValue(userInfo)}
                  readOnly
                />
                <br />
                <label id="user-birthday-info"></label>
                <br />
                <input
                  id="user-birthday-value"
                  value={userInfo.birthday}
                  readOnly
                />
                <br />
                <button id="edit-button" onClick={handleEdit}>
                    
                </button>
              </div>
            )}
            <hr style={{ marginTop: "50px" }} />
            {/*    (accessToken )    */}
            {hasAccessToken ? null : (
              <>
                <button id="Change-password-button" onClick={toggleModal}>
                   
                </button>
                <PasswordModal isOpen={isModalOpen} onClose={toggleModal} />
              </>
            )}
            <button id="signOut-button" onClick={handleSignOut}>
               
            </button>
          </section>
        </div>
      </main>
    </div>
  );
}
